import { createClient } from '@supabase/supabase-js'

const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
const supabaseAnonKey = import.meta.env.VITE_SUPABASE_ANON_KEY

console.log(' Supabase Config:', {
  url: supabaseUrl ? `${supabaseUrl.substring(0, 20)}...` : 'undefined',
  hasKey: !!supabaseAnonKey,
  keyLength: supabaseAnonKey?.length || 0
})

if (!supabaseUrl || !supabaseAnonKey) {
  console.error(' Missing Supabase environment variables:', {
    url: supabaseUrl,
    hasKey: !!supabaseAnonKey
  })
  throw new Error('Missing Supabase environment variables')
}

export const supabase = createClient(supabaseUrl, supabaseAnonKey, {
  auth: {
    autoRefreshToken: true,
    persistSession: true,
    detectSessionInUrl: true
  }
})

console.log(' Supabase client created successfully')

// Helper functions for common operations
export const supabaseHelpers = {
  // Get current user
  async getCurrentUser() {
    const { data: { user }, error } = await supabase.auth.getUser()
    if (error) throw error
    return user
  },

  // Sign in with email/password
  async signIn(email, password) {
    const { data, error } = await supabase.auth.signInWithPassword({
      email,
      password
    })
    if (error) throw error
    return data
  },

  // Sign up with email/password
  async signUp(email, password, metadata = {}) {
    const { data, error } = await supabase.auth.signUp({
      email,
      password,
      options: {
        data: metadata
      }
    })
    if (error) throw error
    return data
  },

  // Sign out
  async signOut() {
    const { error } = await supabase.auth.signOut()
    if (error) throw error
  },

  // Get models from database
  async getModels(options = {}) {
    let query = supabase
      .from('models')
      .select(`
        *,
        creator:profiles!models_user_id_fkey(
          id,
          username,
          avatar_url,
          bio
        )
      `)

    if (options.category) {
      query = query.eq('category', options.category)
    }

    if (options.featured) {
      query = query.eq('is_featured', true)
    }

    if (options.trending) {
      query = query.order('view_count', { ascending: false })
    }

    if (options.limit) {
      query = query.limit(options.limit)
    }

    const { data, error } = await query
    if (error) throw error
    return data
  },

  // Get platform statistics
  async getStats() {
    const { data, error } = await supabase
      .from('models')
      .select('*', { count: 'exact', head: true })

    if (error) throw error

    // Get category counts
    const { data: categories } = await supabase
      .from('models')
      .select('category')

    const categoryCounts = categories?.reduce((acc, model) => {
      acc[model.category] = (acc[model.category] || 0) + 1
      return acc
    }, {}) || {}

    return {
      overview: {
        totalModels: data?.length || 0,
        totalUsers: 0, // Will implement later
        totalDownloads: 0, // Will implement later
        totalViews: 0, // Will implement later
        totalLikes: 0, // Will implement later
        totalComments: 0 // Will implement later
      },
      topCategories: Object.entries(categoryCounts)
        .map(([category, count]) => ({ category, count }))
        .sort((a, b) => b.count - a.count)
        .slice(0, 5)
    }
  }
}
