import { useState, useEffect } from 'react';
import { Download, Calendar, Eye, Package, TrendingUp } from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import { downloadService } from '../services/downloadService';
import { Link } from 'react-router-dom';

const DownloadHistory = () => {
  const [downloads, setDownloads] = useState([]);
  const [loading, setLoading] = useState(true);
  const [stats, setStats] = useState({
    total: 0,
    thisWeek: 0,
    thisMonth: 0
  });
  const { user, isAuthenticated } = useAuth();

  useEffect(() => {
    if (isAuthenticated) {
      fetchDownloadHistory();
    }
  }, [isAuthenticated]);

  const fetchDownloadHistory = async () => {
    try {
      setLoading(true);
      
      const data = await downloadService.getUserDownloadHistory();
      setDownloads(data);

      // Calculate stats
      const now = new Date();
      const weekAgo = new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
      const monthAgo = new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);

      const thisWeek = data.filter(d => new Date(d.downloaded_at) >= weekAgo).length;
      const thisMonth = data.filter(d => new Date(d.downloaded_at) >= monthAgo).length;

      setStats({
        total: data.length,
        thisWeek,
        thisMonth
      });

    } catch (error) {
      console.error('Error fetching download history:', error);
    } finally {
      setLoading(false);
    }
  };

  const formatDate = (dateString) => {
    const date = new Date(dateString);
    return date.toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric',
      hour: '2-digit',
      minute: '2-digit'
    });
  };

  const getFileSize = (bytes) => {
    if (!bytes) return 'Unknown';
    const sizes = ['Bytes', 'KB', 'MB', 'GB'];
    const i = Math.floor(Math.log(bytes) / Math.log(1024));
    return Math.round(bytes / Math.pow(1024, i) * 100) / 100 + ' ' + sizes[i];
  };

  if (!isAuthenticated) {
    return (
      <div className="min-h-screen bg-secondary-50 dark:bg-secondary-900 flex items-center justify-center">
        <div className="text-center">
          <Package className="w-16 h-16 text-secondary-400 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-2">
            Access Denied
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-4">
            Please log in to view your download history.
          </p>
          <Link
            to="/login"
            className="inline-flex items-center px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
          >
            Go to Login
          </Link>
        </div>
      </div>
    );
  }

  if (loading) {
    return (
      <div className="min-h-screen bg-secondary-50 dark:bg-secondary-900 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-primary-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-secondary-600 dark:text-secondary-400">Loading download history...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-secondary-900 dark:text-white mb-2">
          Download History
        </h1>
        <p className="text-secondary-600 dark:text-secondary-400">
          Track all the 3D models you've downloaded
        </p>
      </div>

      {/* Stats Cards */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
        <div className="bg-white dark:bg-secondary-800 rounded-lg p-6 shadow-sm border border-secondary-200 dark:border-secondary-700">
          <div className="flex items-center">
            <div className="p-2 bg-blue-100 dark:bg-blue-900 rounded-lg">
              <Download className="w-6 h-6 text-blue-600 dark:text-blue-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-secondary-600 dark:text-secondary-400">
                Total Downloads
              </p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                {stats.total}
              </p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-secondary-800 rounded-lg p-6 shadow-sm border border-secondary-200 dark:border-secondary-700">
          <div className="flex items-center">
            <div className="p-2 bg-green-100 dark:bg-green-900 rounded-lg">
              <TrendingUp className="w-6 h-6 text-green-600 dark:text-green-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-secondary-600 dark:text-secondary-400">
                This Week
              </p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                {stats.thisWeek}
              </p>
            </div>
          </div>
        </div>

        <div className="bg-white dark:bg-secondary-800 rounded-lg p-6 shadow-sm border border-secondary-200 dark:border-secondary-700">
          <div className="flex items-center">
            <div className="p-2 bg-purple-100 dark:bg-purple-900 rounded-lg">
              <Calendar className="w-6 h-6 text-purple-600 dark:text-purple-400" />
            </div>
            <div className="ml-4">
              <p className="text-sm font-medium text-secondary-600 dark:text-secondary-400">
                This Month
              </p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                {stats.thisMonth}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Download History */}
      <div className="bg-white dark:bg-secondary-800 rounded-lg shadow-sm border border-secondary-200 dark:border-secondary-700">
        <div className="px-6 py-4 border-b border-secondary-200 dark:border-secondary-700">
          <h2 className="text-lg font-semibold text-secondary-900 dark:text-white">
            Recent Downloads
          </h2>
        </div>

        {downloads.length === 0 ? (
          <div className="p-8 text-center">
            <Package className="w-16 h-16 text-secondary-400 mx-auto mb-4" />
            <h3 className="text-lg font-medium text-secondary-900 dark:text-white mb-2">
              No downloads yet
            </h3>
            <p className="text-secondary-600 dark:text-secondary-400 mb-4">
              Start exploring and downloading 3D models to see them here.
            </p>
            <Link
              to="/explore"
              className="inline-flex items-center px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
            >
              Explore Models
            </Link>
          </div>
        ) : (
          <div className="divide-y divide-secondary-200 dark:divide-secondary-700">
            {downloads.map((download) => (
              <div key={download.id} className="p-6 hover:bg-secondary-50 dark:hover:bg-secondary-700 transition-colors">
                <div className="flex items-start space-x-4">
                  {download.model?.thumbnail_url ? (
                    <img
                      src={download.model.thumbnail_url}
                      alt={download.model.title}
                      className="w-16 h-16 object-cover rounded-lg bg-secondary-200 dark:bg-secondary-600"
                    />
                  ) : (
                    <div className="w-16 h-16 bg-secondary-200 dark:bg-secondary-600 rounded-lg flex items-center justify-center">
                      <Package className="w-8 h-8 text-secondary-400" />
                    </div>
                  )}
                  
                  <div className="flex-1 min-w-0">
                    <div className="flex items-start justify-between">
                      <div>
                        <h3 className="text-lg font-medium text-secondary-900 dark:text-white mb-1">
                          {download.model?.title || 'Unknown Model'}
                        </h3>
                        <p className="text-sm text-secondary-600 dark:text-secondary-400 mb-2">
                          {download.model?.description || 'No description available'}
                        </p>
                        <div className="flex items-center space-x-4 text-sm text-secondary-500 dark:text-secondary-400">
                          <span className="flex items-center">
                            <Calendar className="w-4 h-4 mr-1" />
                            {formatDate(download.downloaded_at)}
                          </span>
                          {download.model?.category && (
                            <span className="px-2 py-1 bg-secondary-100 dark:bg-secondary-700 rounded-full text-xs">
                              {download.model.category}
                            </span>
                          )}
                        </div>
                      </div>
                      
                      <div className="flex items-center space-x-2">
                        <Link
                          to={`/models/${download.model_id}`}
                          className="inline-flex items-center px-3 py-2 text-sm font-medium text-secondary-700 dark:text-secondary-300 hover:text-secondary-900 dark:hover:text-white transition-colors"
                        >
                          <Eye className="w-4 h-4 mr-1" />
                          View
                        </Link>
                        <button
                          onClick={() => window.open(download.model?.file_url, '_blank')}
                          className="inline-flex items-center px-3 py-2 text-sm font-medium text-primary-600 hover:text-primary-700 transition-colors"
                        >
                          <Download className="w-4 h-4 mr-1" />
                          Download Again
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default DownloadHistory;
