import { useState } from 'react';
import { Download, CheckCircle, AlertCircle, Loader2 } from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import { downloadService } from '../services/downloadService';
import toast from 'react-hot-toast';

const DownloadButton = ({ model, className = '' }) => {
  const [downloading, setDownloading] = useState(false);
  const [downloaded, setDownloaded] = useState(false);
  const [error, setError] = useState(null);
  const { user, isAuthenticated } = useAuth();

  const handleDownload = async () => {
    if (!isAuthenticated) {
      toast.error('Please log in to download models');
      return;
    }

    if (!model.file_url) {
      toast.error('No file available for download');
      return;
    }

    setDownloading(true);
    setError(null);

    try {
      await downloadService.downloadModel(model.id, model);
      
      setDownloaded(true);
      toast.success('Download completed successfully!');
      
      // Reset downloaded state after 3 seconds
      setTimeout(() => setDownloaded(false), 3000);

    } catch (err) {
      console.error('Download error:', err);
      setError(err.message);
      toast.error('Download failed. Please try again.');
    } finally {
      setDownloading(false);
    }
  };

  const getButtonContent = () => {
    if (downloaded) {
      return (
        <>
          <CheckCircle className="w-4 h-4" />
          Downloaded
        </>
      );
    }

    if (downloading) {
      return (
        <>
          <Loader2 className="w-4 h-4 animate-spin" />
          Downloading...
        </>
      );
    }

    if (error) {
      return (
        <>
          <AlertCircle className="w-4 h-4" />
          Retry Download
        </>
      );
    }

    return (
      <>
        <Download className="w-4 h-4" />
        Download
      </>
    );
  };

  const getButtonClass = () => {
    let baseClass = 'inline-flex items-center gap-2 px-4 py-2 rounded-lg font-medium transition-all duration-200 focus:outline-none focus:ring-2 focus:ring-offset-2';
    
    if (downloaded) {
      return `${baseClass} bg-green-600 text-white hover:bg-green-700 focus:ring-green-500`;
    }
    
    if (downloading) {
      return `${baseClass} bg-blue-600 text-white cursor-not-allowed opacity-75`;
    }
    
    if (error) {
      return `${baseClass} bg-red-600 text-white hover:bg-red-700 focus:ring-red-500`;
    }
    
    return `${baseClass} bg-primary-600 text-white hover:bg-primary-700 focus:ring-primary-500 ${className}`;
  };

  return (
    <div className="space-y-2">
      <button
        onClick={handleDownload}
        disabled={downloading}
        className={getButtonClass()}
      >
        {getButtonContent()}
      </button>
      
      {model.download_count > 0 && (
        <p className="text-sm text-secondary-600 dark:text-secondary-400 text-center">
          {model.download_count} download{model.download_count !== 1 ? 's' : ''}
        </p>
      )}
      
      {error && (
        <p className="text-sm text-red-600 dark:text-red-400 text-center">
          {error}
        </p>
      )}
    </div>
  );
};

export default DownloadButton;
