import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { supabase } from '../lib/supabase'
import { 
  Edit3, 
  Trash2, 
  Eye, 
  Upload, 
  Search,
  Filter,
  Grid3X3,
  List,
  Plus,
  ArrowLeft,
  AlertCircle,
  CheckCircle,
  Loader2,
  BarChart3
} from 'lucide-react'
import toast from 'react-hot-toast'

const ModelManagement = () => {
  const { user, profile } = useAuth()
  const navigate = useNavigate()
  
  const [models, setModels] = useState([])
  const [loading, setLoading] = useState(true)
  const [searchTerm, setSearchTerm] = useState('')
  const [categoryFilter, setCategoryFilter] = useState('all')
  const [viewMode, setViewMode] = useState('grid') // 'grid' or 'list'
  const [deletingModel, setDeletingModel] = useState(null)

  // Load user's models
  useEffect(() => {
    if (user) {
      fetchUserModels()
    }
  }, [user])

  const fetchUserModels = async () => {
    try {
      setLoading(true)
      const { data, error } = await supabase
        .from('models')
        .select(`
          *,
          creator:profiles!models_user_id_fkey(username, avatar_url)
        `)
        .eq('user_id', user.id)
        .order('created_at', { ascending: false })

      if (error) throw error

      setModels(data || [])
      console.log(' User models loaded:', data)
    } catch (error) {
      console.error(' Error fetching models:', error)
      toast.error('Failed to load your models')
    } finally {
      setLoading(false)
    }
  }

  // Filter models based on search and category
  const filteredModels = models.filter(model => {
    const matchesSearch = model.title.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         model.description.toLowerCase().includes(searchTerm.toLowerCase()) ||
                         model.tags.toLowerCase().includes(searchTerm.toLowerCase())
    
    const matchesCategory = categoryFilter === 'all' || model.category === categoryFilter
    
    return matchesSearch && matchesCategory
  })

  // Delete model
  const handleDeleteModel = async (modelId, modelTitle) => {
    if (!confirm(`Are you sure you want to delete "${modelTitle}"? This action cannot be undone.`)) {
      return
    }

    try {
      setDeletingModel(modelId)
      
      // Delete from storage first
      const { error: storageError } = await supabase.storage
        .from('models')
        .remove([`${user.id}/${modelId}.obj`])

      if (storageError && storageError.code !== 'PGRST116') {
        console.warn('Storage deletion warning:', storageError)
      }

      // Delete thumbnail if exists
      const { error: thumbnailError } = await supabase.storage
        .from('thumbnails')
        .remove([`${user.id}/${modelId}.jpg`])

      if (thumbnailError && thumbnailError.code !== 'PGRST116') {
        console.warn('Thumbnail deletion warning:', thumbnailError)
      }

      // Delete preview if exists
      const { error: previewError } = await supabase.storage
        .from('previews')
        .remove([`${user.id}/${modelId}.jpg`])

      if (previewError && previewError.code !== 'PGRST116') {
        console.warn('Preview deletion warning:', previewError)
      }

      // Delete from database
      const { error: dbError } = await supabase
        .from('models')
        .delete()
        .eq('id', modelId)

      if (dbError) throw dbError

      toast.success('Model deleted successfully')
      
      // Refresh models list
      await fetchUserModels()
      
    } catch (error) {
      console.error(' Error deleting model:', error)
      toast.error('Failed to delete model')
    } finally {
      setDeletingModel(null)
    }
  }

  // Get category options from existing models
  const categories = ['all', ...new Set(models.map(model => model.category).filter(Boolean))]

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-4">
            Authentication Required
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-6">
            You need to be logged in to manage your models.
          </p>
          <button
            onClick={() => navigate('/login')}
            className="btn-primary"
          >
            Sign In
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => navigate(-1)}
              className="btn-secondary"
            >
              <ArrowLeft className="w-4 h-4 mr-2" />
              Back
            </button>
            <h1 className="text-3xl font-bold text-secondary-900 dark:text-white">
              Model Management
            </h1>
          </div>
          
          <button
            onClick={() => navigate('/upload')}
            className="btn-primary"
          >
            <Plus className="w-4 h-4 mr-2" />
            Upload New Model
          </button>
          <button
            onClick={() => navigate('/analytics')}
            className="btn-secondary"
          >
            <BarChart3 className="w-4 h-4 mr-2" />
            View Analytics
          </button>
        </div>
        
        <p className="text-secondary-600 dark:text-secondary-400">
          Manage your uploaded 3D models. Edit details, delete models, and organize your portfolio.
        </p>
      </div>

      {/* Stats */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4">
          <div className="flex items-center">
            <Grid3X3 className="w-8 h-8 text-primary-600 mr-3" />
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Total Models</p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">{models.length}</p>
            </div>
          </div>
        </div>
        
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4">
          <div className="flex items-center">
            <Eye className="w-8 h-8 text-blue-600 mr-3" />
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Total Views</p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                {models.reduce((sum, model) => sum + (model.views || 0), 0)}
              </p>
            </div>
          </div>
        </div>
        
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4">
          <div className="flex items-center">
            <CheckCircle className="w-8 h-8 text-green-600 mr-3" />
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Published</p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                {models.filter(model => model.status === 'published').length}
              </p>
            </div>
          </div>
        </div>
        
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4">
          <div className="flex items-center">
            <Upload className="w-8 h-8 text-purple-600 mr-3" />
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">This Month</p>
              <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                {models.filter(model => {
                  const modelDate = new Date(model.created_at)
                  const monthAgo = new Date()
                  monthAgo.setMonth(monthAgo.getMonth() - 1)
                  return modelDate > monthAgo
                }).length}
              </p>
            </div>
          </div>
        </div>
      </div>

      {/* Filters and Search */}
      <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4 mb-6">
        <div className="flex flex-col md:flex-row md:items-center space-y-4 md:space-y-0 md:space-x-4">
          {/* Search */}
          <div className="flex-1">
            <div className="relative">
              <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 w-5 h-5 text-secondary-400" />
              <input
                type="text"
                placeholder="Search models by title, description, or tags..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-full pl-10 pr-4 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
              />
            </div>
          </div>

          {/* Category Filter */}
          <div className="flex items-center space-x-2">
            <Filter className="w-5 h-5 text-secondary-400" />
            <select
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
              className="px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
            >
              {categories.map(category => (
                <option key={category} value={category}>
                  {category === 'all' ? 'All Categories' : category}
                </option>
              ))}
            </select>
          </div>

          {/* View Mode Toggle */}
          <div className="flex items-center space-x-2">
            <button
              onClick={() => setViewMode('grid')}
              className={`p-2 rounded-lg transition-colors ${
                viewMode === 'grid' 
                  ? 'bg-primary-100 text-primary-600 dark:bg-primary-900 dark:text-primary-400' 
                  : 'text-secondary-400 hover:text-secondary-600 dark:hover:text-secondary-300'
              }`}
            >
              <Grid3X3 className="w-5 h-5" />
            </button>
            <button
              onClick={() => setViewMode('list')}
              className={`p-2 rounded-lg transition-colors ${
                viewMode === 'list' 
                  ? 'bg-primary-100 text-primary-600 dark:bg-primary-900 dark:text-primary-400' 
                  : 'text-secondary-400 hover:text-secondary-600 dark:hover:text-secondary-300'
              }`}
            >
              <List className="w-5 h-5" />
            </button>
          </div>
        </div>
      </div>

      {/* Models List */}
      {loading ? (
        <div className="flex items-center justify-center py-12">
          <Loader2 className="w-8 h-8 animate-spin text-primary-600" />
          <span className="ml-2 text-secondary-600 dark:text-secondary-400">Loading your models...</span>
        </div>
      ) : filteredModels.length === 0 ? (
        <div className="text-center py-12">
          <Upload className="w-16 h-16 text-secondary-400 mx-auto mb-4" />
          <h3 className="text-xl font-semibold text-secondary-900 dark:text-white mb-2">
            {searchTerm || categoryFilter !== 'all' ? 'No models found' : 'No models yet'}
          </h3>
          <p className="text-secondary-600 dark:text-secondary-400 mb-6">
            {searchTerm || categoryFilter !== 'all' 
              ? 'Try adjusting your search or filters'
              : 'Start building your portfolio by uploading your first 3D model'
            }
          </p>
          {!searchTerm && categoryFilter === 'all' && (
            <button
              onClick={() => navigate('/upload')}
              className="btn-primary"
            >
              <Plus className="w-4 h-4 mr-2" />
              Upload Your First Model
            </button>
          )}
        </div>
      ) : (
        <div className={viewMode === 'grid' ? 'grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6' : 'space-y-4'}>
          {filteredModels.map(model => (
            <ModelCard
              key={model.id}
              model={model}
              viewMode={viewMode}
              onEdit={() => navigate(`/models/${model.id}/edit`)}
              onDelete={() => handleDeleteModel(model.id, model.title)}
              onView={() => navigate(`/models/${model.id}`)}
              isDeleting={deletingModel === model.id}
            />
          ))}
        </div>
      )}
    </div>
  )
}

// Model Card Component
const ModelCard = ({ model, viewMode, onEdit, onDelete, onView, isDeleting }) => {
  const formatFileSize = (bytes) => {
    if (bytes === 0) return '0 Bytes'
    const k = 1024
    const sizes = ['Bytes', 'KB', 'MB', 'GB']
    const i = Math.floor(Math.log(bytes) / Math.log(k))
    return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
  }

  const formatDate = (dateString) => {
    return new Date(dateString).toLocaleDateString('en-US', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    })
  }

  if (viewMode === 'list') {
    return (
      <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4">
        <div className="flex items-center space-x-4">
          {/* Thumbnail */}
          <div className="w-20 h-20 rounded-lg overflow-hidden bg-secondary-200 dark:bg-secondary-700 flex-shrink-0">
            {model.thumbnail_url ? (
              <img 
                src={model.thumbnail_url} 
                alt={model.title}
                className="w-full h-full object-cover"
              />
            ) : (
              <div className="w-full h-full flex items-center justify-center">
                <Upload className="w-8 h-8 text-secondary-400" />
              </div>
            )}
          </div>

          {/* Model Info */}
          <div className="flex-1 min-w-0">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white truncate">
              {model.title}
            </h3>
            <p className="text-sm text-secondary-600 dark:text-secondary-400 line-clamp-2 mb-2">
              {model.description}
            </p>
            <div className="flex items-center space-x-4 text-sm text-secondary-500">
              <span className="bg-secondary-100 dark:bg-secondary-700 px-2 py-1 rounded">
                {model.category}
              </span>
              <span>{formatFileSize(model.file_size)}</span>
              <span>{formatDate(model.created_at)}</span>
            </div>
          </div>

          {/* Stats */}
          <div className="flex items-center space-x-4 text-sm text-secondary-500">
            <div className="text-center">
              <div className="font-semibold text-secondary-900 dark:text-white">{model.views || 0}</div>
              <div>Views</div>
            </div>
            <div className="text-center">
              <div className="font-semibold text-secondary-900 dark:text-white">{model.likes || 0}</div>
              <div>Likes</div>
            </div>
          </div>

          {/* Actions */}
          <div className="flex items-center space-x-2">
            <button
              onClick={onView}
              className="p-2 text-secondary-400 hover:text-secondary-600 dark:hover:text-secondary-300 transition-colors"
              title="View Model"
            >
              <Eye className="w-4 h-4" />
            </button>
            <button
              onClick={onEdit}
              className="p-2 text-blue-400 hover:text-blue-600 transition-colors"
              title="Edit Model"
            >
              <Edit3 className="w-4 h-4" />
            </button>
            <button
              onClick={onDelete}
              disabled={isDeleting}
              className="p-2 text-red-400 hover:text-red-600 disabled:opacity-50 transition-colors"
              title="Delete Model"
            >
              {isDeleting ? (
                <Loader2 className="w-4 h-4 animate-spin" />
              ) : (
                <Trash2 className="w-4 h-4" />
              )}
            </button>
          </div>
        </div>
      </div>
    )
  }

  // Grid view
  return (
    <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 overflow-hidden hover:shadow-lg transition-shadow">
      {/* Thumbnail */}
      <div className="aspect-video bg-secondary-200 dark:bg-secondary-700 relative group">
        {model.thumbnail_url ? (
          <img 
            src={model.thumbnail_url} 
            alt={model.title}
            className="w-full h-full object-cover"
          />
        ) : (
          <div className="w-full h-full flex items-center justify-center">
            <Upload className="w-16 h-16 text-secondary-400" />
          </div>
        )}
        
        {/* Overlay Actions */}
        <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-50 transition-all duration-200 flex items-center justify-center">
          <div className="flex space-x-2 opacity-0 group-hover:opacity-100 transition-opacity duration-200">
            <button
              onClick={onView}
              className="p-2 bg-white text-secondary-900 rounded-lg hover:bg-secondary-100 transition-colors"
              title="View Model"
            >
              <Eye className="w-4 h-4" />
            </button>
            <button
              onClick={onEdit}
              className="p-2 bg-blue-500 text-white rounded-lg hover:bg-blue-600 transition-colors"
              title="Edit Model"
            >
              <Edit3 className="w-4 h-4" />
            </button>
            <button
              onClick={onDelete}
              disabled={isDeleting}
              className="p-2 bg-red-500 text-white rounded-lg hover:bg-red-600 disabled:opacity-50 transition-colors"
              title="Delete Model"
            >
              {isDeleting ? (
                <Loader2 className="w-4 h-4 animate-spin" />
              ) : (
                <Trash2 className="w-4 h-4" />
              )}
            </button>
          </div>
        </div>
      </div>

      {/* Content */}
      <div className="p-4">
        <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-2 line-clamp-1">
          {model.title}
        </h3>
        <p className="text-sm text-secondary-600 dark:text-secondary-400 line-clamp-2 mb-3">
          {model.description}
        </p>
        
        <div className="flex items-center justify-between text-sm text-secondary-500 mb-3">
          <span className="bg-secondary-100 dark:bg-secondary-700 px-2 py-1 rounded">
            {model.category}
          </span>
          <span>{formatFileSize(model.file_size)}</span>
        </div>
        
        <div className="flex items-center justify-between text-sm">
          <span className="text-secondary-500">{formatDate(model.created_at)}</span>
          <div className="flex items-center space-x-4">
            <span className="flex items-center">
              <Eye className="w-4 h-4 mr-1" />
              {model.views || 0}
            </span>
            <span className="flex items-center">
              <CheckCircle className="w-4 h-4 mr-1" />
              {model.likes || 0}
            </span>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ModelManagement
