import { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import { Eye, Download, Heart, MessageCircle, Calendar, User, Tag, Package } from 'lucide-react';
import { useAuth } from '../contexts/AuthContext';
import DownloadButton from '../components/DownloadButton';
import toast from 'react-hot-toast';

const ModelDetail = () => {
  const { modelId } = useParams();
  const [model, setModel] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const { user, isAuthenticated } = useAuth();

  useEffect(() => {
    if (modelId) {
      fetchModelDetails();
    }
  }, [modelId]);

  const fetchModelDetails = async () => {
    try {
      setLoading(true);
      
      // Fetch model details from your API
      const response = await fetch(`/api/models/${modelId}`);
      
      if (!response.ok) {
        throw new Error('Failed to fetch model details');
      }

      const data = await response.json();
      setModel(data);
    } catch (err) {
      console.error('Error fetching model:', err);
      setError(err.message);
      toast.error('Failed to load model details');
    } finally {
      setLoading(false);
    }
  };

  const handleLike = async () => {
    if (!isAuthenticated) {
      toast.error('Please log in to like models');
      return;
    }

    try {
      const response = await fetch(`/api/models/${modelId}/like`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${user.access_token || 'local'}`
        }
      });

      if (response.ok) {
        // Refresh model data to get updated like count
        fetchModelDetails();
        toast.success('Model liked successfully!');
      }
    } catch (error) {
      console.error('Error liking model:', error);
      toast.error('Failed to like model');
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen bg-secondary-50 dark:bg-secondary-900 flex items-center justify-center">
        <div className="text-center">
          <div className="w-8 h-8 border-4 border-primary-600 border-t-transparent rounded-full animate-spin mx-auto mb-4"></div>
          <p className="text-secondary-600 dark:text-secondary-400">Loading model details...</p>
        </div>
      </div>
    );
  }

  if (error || !model) {
    return (
      <div className="min-h-screen bg-secondary-50 dark:bg-secondary-900 flex items-center justify-center">
        <div className="text-center">
          <Package className="w-16 h-16 text-secondary-400 mx-auto mb-4" />
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-2">
            Model Not Found
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-4">
            {error || 'The model you are looking for could not be found.'}
          </p>
          <Link
            to="/explore"
            className="inline-flex items-center px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
          >
            Explore Models
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="max-w-6xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Breadcrumb */}
      <nav className="flex items-center space-x-2 text-sm text-secondary-600 dark:text-secondary-400 mb-6">
        <Link to="/" className="hover:text-secondary-900 dark:hover:text-white transition-colors">
          Home
        </Link>
        <span>/</span>
        <Link to="/explore" className="hover:text-secondary-900 dark:hover:text-white transition-colors">
          Explore
        </Link>
        <span>/</span>
        <span className="text-secondary-900 dark:text-white">{model.title}</span>
      </nav>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
        {/* Left Column - Model Preview */}
        <div className="lg:col-span-2 space-y-6">
          {/* 3D Preview */}
          <div className="bg-white dark:bg-secondary-800 rounded-lg shadow-sm border border-secondary-200 dark:border-secondary-700 overflow-hidden">
            <div className="aspect-video bg-secondary-100 dark:bg-secondary-700 flex items-center justify-center">
              {model.preview_url ? (
                <img
                  src={model.preview_url}
                  alt={model.title}
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="text-center">
                  <Package className="w-16 h-16 text-secondary-400 mx-auto mb-2" />
                  <p className="text-secondary-600 dark:text-secondary-400">
                    3D Preview
                  </p>
                  <p className="text-sm text-secondary-500 dark:text-secondary-400">
                    Preview not available
                  </p>
                </div>
              )}
            </div>
          </div>

          {/* Model Information */}
          <div className="bg-white dark:bg-secondary-800 rounded-lg shadow-sm border border-secondary-200 dark:border-secondary-700 p-6">
            <h1 className="text-3xl font-bold text-secondary-900 dark:text-white mb-4">
              {model.title}
            </h1>
            
            <p className="text-secondary-600 dark:text-secondary-400 mb-6">
              {model.description || 'No description available.'}
            </p>

            {/* Tags */}
            {model.tags && (
              <div className="mb-6">
                <h3 className="text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                  Tags
                </h3>
                <div className="flex flex-wrap gap-2">
                  {model.tags.split(',').map((tag, index) => (
                    <span
                      key={index}
                      className="px-3 py-1 bg-secondary-100 dark:bg-secondary-700 text-secondary-700 dark:text-secondary-300 rounded-full text-sm"
                    >
                      {tag.trim()}
                    </span>
                  ))}
                </div>
              </div>
            )}

            {/* Model Stats */}
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4 py-4 border-t border-secondary-200 dark:border-secondary-700">
              <div className="text-center">
                <div className="flex items-center justify-center w-8 h-8 bg-blue-100 dark:bg-blue-900 rounded-lg mx-auto mb-2">
                  <Eye className="w-4 h-4 text-blue-600 dark:text-blue-400" />
                </div>
                <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                  {model.view_count || 0}
                </p>
                <p className="text-xs text-secondary-500 dark:text-secondary-400">Views</p>
              </div>
              
              <div className="text-center">
                <div className="flex items-center justify-center w-8 h-8 bg-green-100 dark:bg-green-900 rounded-lg mx-auto mb-2">
                  <Download className="w-4 h-4 text-green-600 dark:text-green-400" />
                </div>
                <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                  {model.download_count || 0}
                </p>
                <p className="text-xs text-secondary-500 dark:text-secondary-400">Downloads</p>
              </div>
              
              <div className="text-center">
                <div className="flex items-center justify-center w-8 h-8 bg-red-100 dark:bg-red-900 rounded-lg mx-auto mb-2">
                  <Heart className="w-4 h-4 text-red-600 dark:text-red-400" />
                </div>
                <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                  {model.like_count || 0}
                </p>
                <p className="text-xs text-secondary-500 dark:text-secondary-400">Likes</p>
              </div>
              
              <div className="text-center">
                <div className="flex items-center justify-center w-8 h-8 bg-purple-100 dark:bg-purple-900 rounded-lg mx-auto mb-2">
                  <MessageCircle className="w-4 h-4 text-purple-600 dark:text-purple-400" />
                </div>
                <p className="text-2xl font-bold text-secondary-900 dark:text-white">
                  {model.comment_count || 0}
                </p>
                <p className="text-xs text-secondary-500 dark:text-secondary-400">Comments</p>
              </div>
            </div>
          </div>
        </div>

        {/* Right Column - Download & Creator Info */}
        <div className="space-y-6">
          {/* Download Section */}
          <div className="bg-white dark:bg-secondary-800 rounded-lg shadow-sm border border-secondary-200 dark:border-secondary-700 p-6">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
              Download Model
            </h3>
            
            <DownloadButton model={model} className="w-full" />
            
            {/* File Info */}
            <div className="mt-4 space-y-2 text-sm text-secondary-600 dark:text-secondary-400">
              <div className="flex justify-between">
                <span>File Type:</span>
                <span className="font-medium">{model.file_type?.toUpperCase() || 'Unknown'}</span>
              </div>
              <div className="flex justify-between">
                <span>File Size:</span>
                <span className="font-medium">
                  {model.file_size ? `${(model.file_size / 1024 / 1024).toFixed(2)} MB` : 'Unknown'}
                </span>
              </div>
              <div className="flex justify-between">
                <span>Category:</span>
                <span className="font-medium">{model.category || 'Uncategorized'}</span>
              </div>
            </div>
          </div>

          {/* Creator Info */}
          <div className="bg-white dark:bg-secondary-800 rounded-lg shadow-sm border border-secondary-200 dark:border-secondary-700 p-6">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
              Creator
            </h3>
            
            <div className="flex items-center space-x-3 mb-4">
              {model.creator?.avatar_url ? (
                <img
                  src={model.creator.avatar_url}
                  alt={model.creator.username}
                  className="w-12 h-12 rounded-full object-cover"
                />
              ) : (
                <div className="w-12 h-12 bg-secondary-200 dark:bg-secondary-700 rounded-full flex items-center justify-center">
                  <User className="w-6 h-6 text-secondary-400" />
                </div>
              )}
              
              <div>
                <Link
                  to={`/profile/${model.creator?.username}`}
                  className="font-medium text-secondary-900 dark:text-white hover:text-primary-600 dark:hover:text-primary-400 transition-colors"
                >
                  {model.creator?.username || 'Unknown Creator'}
                </Link>
                {model.creator?.bio && (
                  <p className="text-sm text-secondary-600 dark:text-secondary-400">
                    {model.creator.bio}
                  </p>
                )}
              </div>
            </div>
            
            <Link
              to={`/profile/${model.creator?.username}`}
              className="w-full inline-flex items-center justify-center px-4 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg text-sm font-medium text-secondary-700 dark:text-secondary-300 hover:bg-secondary-50 dark:hover:bg-secondary-700 transition-colors"
            >
              View Profile
            </Link>
          </div>

          {/* Model Details */}
          <div className="bg-white dark:bg-secondary-800 rounded-lg shadow-sm border border-secondary-200 dark:border-secondary-700 p-6">
            <h3 className="text-lg font-semibold text-secondary-900 dark:text-white mb-4">
              Model Details
            </h3>
            
            <div className="space-y-3 text-sm">
              <div className="flex items-center space-x-2">
                <Calendar className="w-4 h-4 text-secondary-400" />
                <span className="text-secondary-600 dark:text-secondary-400">
                  Created: {new Date(model.created_at).toLocaleDateString()}
                </span>
              </div>
              
              <div className="flex items-center space-x-2">
                <Tag className="w-4 h-4 text-secondary-400" />
                <span className="text-secondary-600 dark:text-secondary-400">
                  Category: {model.category || 'Uncategorized'}
                </span>
              </div>
              
              {model.is_featured && (
                <div className="flex items-center space-x-2">
                  <div className="w-4 h-4 bg-yellow-400 rounded-full"></div>
                  <span className="text-yellow-600 dark:text-yellow-400 font-medium">
                    Featured Model
                  </span>
                </div>
              )}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ModelDetail;
