import { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Package, Eye, Download, Heart, TrendingUp, Star, Users } from 'lucide-react'
import { supabaseHelpers } from '../lib/supabase'
import ModelCard from '../components/ModelCard'
import toast from 'react-hot-toast'

const Home = () => {
  const [featuredModels, setFeaturedModels] = useState([])
  const [trendingModels, setTrendingModels] = useState([])
  const [stats, setStats] = useState(null)
  const [featuredLoading, setFeaturedLoading] = useState(true)
  const [trendingLoading, setTrendingLoading] = useState(true)
  const [statsLoading, setStatsLoading] = useState(true)

  useEffect(() => {
    fetchData()
  }, [])

  const fetchData = async () => {
    try {
      // Fetch featured models from Supabase
      const featuredData = await supabaseHelpers.getModels({ featured: true, limit: 6 })
      setFeaturedModels(featuredData || [])
      setFeaturedLoading(false)

      // Fetch trending models from Supabase
      const trendingData = await supabaseHelpers.getModels({ trending: true, limit: 6 })
      setTrendingModels(trendingData || [])
      setTrendingLoading(false)

      // Fetch stats from Supabase
      const statsData = await supabaseHelpers.getStats()
      setStats(statsData)
      setStatsLoading(false)

      console.log('Featured models loaded:', { models: featuredData })
      console.log('Trending models loaded:', { models: trendingData })
      console.log('Stats loaded:', statsData)
    } catch (error) {
      console.error('Error fetching data:', error)
      toast.error('Failed to load data. Please refresh the page.')
      setFeaturedLoading(false)
      setTrendingLoading(false)
      setStatsLoading(false)
    }
  }

  const StatsCard = ({ icon: Icon, title, value, description }) => (
    <div className="bg-white dark:bg-secondary-800 rounded-lg p-6 shadow-sm border border-secondary-200 dark:border-secondary-700">
      <div className="flex items-center space-x-3">
        <div className="p-2 bg-primary-100 dark:bg-primary-900 rounded-lg">
          <Icon className="w-6 h-6 text-primary-600 dark:text-primary-400" />
        </div>
        <div>
          <h3 className="text-lg font-semibold text-secondary-900 dark:text-white">{value}</h3>
          <p className="text-sm text-secondary-600 dark:text-secondary-400">{title}</p>
        </div>
      </div>
      {description && (
        <p className="text-xs text-secondary-500 dark:text-secondary-500 mt-2">{description}</p>
      )}
    </div>
  )

  const handleRefresh = () => {
    setFeaturedLoading(true)
    setTrendingLoading(true)
    setStatsLoading(true)
    fetchData()
  }

  return (
    <div className="min-h-screen bg-secondary-50 dark:bg-secondary-900">
      {/* Hero Section */}
      <section className="bg-gradient-to-br from-primary-600 via-primary-700 to-primary-800 text-white py-20">
        <div className="container mx-auto px-4 text-center">
          <h1 className="text-4xl md:text-6xl font-bold mb-6">
            Find the Perfect 3D Model for Your Project
          </h1>
          <p className="text-xl md:text-2xl mb-8 text-primary-100 max-w-3xl mx-auto">
            Explore thousands of high-quality 3D models, textures, and materials. All free to download and use in your creative projects.
          </p>
          
          {/* Search Bar */}
          <div className="max-w-2xl mx-auto mb-8">
            <div className="flex">
              <input
                type="text"
                placeholder="Search for models, characters, environments..."
                className="flex-1 px-6 py-4 text-secondary-900 rounded-l-lg focus:outline-none focus:ring-2 focus:ring-primary-400"
              />
              <button className="px-8 py-4 bg-primary-500 hover:bg-primary-400 rounded-r-lg transition-colors duration-200 font-semibold">
                Search
              </button>
            </div>
          </div>

          {/* Category Filters */}
          <div className="flex flex-wrap justify-center gap-3">
            {['All', 'Characters', 'Environments', 'Props', 'Vehicles', 'Weapons'].map((category) => (
              <button
                key={category}
                className={`px-6 py-2 rounded-full transition-colors duration-200 ${
                  category === 'All'
                    ? 'bg-white text-primary-700 font-semibold'
                    : 'bg-white/20 text-white hover:bg-white/30'
                }`}
              >
                {category}
              </button>
            ))}
          </div>
        </div>
      </section>

      {/* Stats Section */}
      <section className="py-16 bg-white dark:bg-secondary-800">
        <div className="container mx-auto px-4">
          <h2 className="text-3xl font-bold text-center text-secondary-900 dark:text-white mb-12">
            Platform Statistics
          </h2>
          
          {statsLoading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              {[...Array(4)].map((_, i) => (
                <div key={i} className="bg-secondary-100 dark:bg-secondary-700 rounded-lg p-6 animate-pulse">
                  <div className="h-4 bg-secondary-300 dark:bg-secondary-600 rounded mb-2"></div>
                  <div className="h-6 bg-secondary-300 dark:bg-secondary-600 rounded"></div>
                </div>
              ))}
            </div>
          ) : stats ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              <StatsCard
                icon={Package}
                title="Total Models"
                value={stats.overview?.totalModels || 0}
                description="High-quality 3D assets"
              />
              <StatsCard
                icon={Users}
                title="Active Creators"
                value={stats.overview?.totalUsers || 0}
                description="Talented artists"
              />
              <StatsCard
                icon={Download}
                title="Downloads"
                value={stats.overview?.totalDownloads || 0}
                description="Models downloaded"
              />
              <StatsCard
                icon={Eye}
                title="Total Views"
                value={stats.overview?.totalViews || 0}
                description="Page views"
              />
            </div>
          ) : (
            <div className="text-center">
              <p className="text-secondary-600 dark:text-secondary-400 mb-4">
                Failed to load statistics
              </p>
              <button
                onClick={handleRefresh}
                className="px-6 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors duration-200"
              >
                Refresh Page
              </button>
            </div>
          )}
        </div>
      </section>

      {/* Featured Models Section */}
      <section className="py-16 bg-secondary-50 dark:bg-secondary-900">
        <div className="container mx-auto px-4">
          <div className="flex items-center justify-between mb-8">
            <h2 className="text-3xl font-bold text-secondary-900 dark:text-white flex items-center">
              <Star className="w-8 h-8 text-yellow-500 mr-3" />
              Featured Models
            </h2>
            <Link
              to="/explore"
              className="text-primary-600 dark:text-primary-400 hover:text-primary-700 dark:hover:text-primary-300 font-semibold flex items-center"
            >
              View All 
            </Link>
          </div>

          {featuredLoading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {[...Array(3)].map((_, i) => (
                <div key={i} className="bg-white dark:bg-secondary-800 rounded-lg p-4 animate-pulse">
                  <div className="h-48 bg-secondary-200 dark:bg-secondary-700 rounded-lg mb-4"></div>
                  <div className="h-4 bg-secondary-200 dark:bg-secondary-700 rounded mb-2"></div>
                  <div className="h-3 bg-secondary-200 dark:bg-secondary-700 rounded w-2/3"></div>
                </div>
              ))}
            </div>
          ) : featuredModels && featuredModels.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {featuredModels.map((model) => (
                <ModelCard key={model.id} model={model} />
              ))}
            </div>
          ) : (
            <div className="text-center py-12">
              <Package className="w-16 h-16 text-secondary-400 dark:text-secondary-500 mx-auto mb-4" />
              <h3 className="text-xl font-semibold text-secondary-600 dark:text-secondary-400 mb-2">
                No featured models yet
              </h3>
              <p className="text-secondary-500 dark:text-secondary-500 mb-6">
                Be the first to upload an amazing 3D model!
              </p>
              <Link
                to="/upload"
                className="inline-flex items-center px-6 py-3 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors duration-200 font-semibold"
              >
                Upload Model
              </Link>
            </div>
          )}
        </div>
      </section>

      {/* Trending Models Section */}
      <section className="py-16 bg-white dark:bg-secondary-800">
        <div className="container mx-auto px-4">
          <div className="flex items-center justify-between mb-8">
            <h2 className="text-3xl font-bold text-secondary-900 dark:text-white flex items-center">
              <TrendingUp className="w-8 h-8 text-green-500 mr-3" />
              Trending Models
            </h2>
            <Link
              to="/explore"
              className="text-primary-600 dark:text-primary-400 hover:text-primary-700 dark:hover:text-primary-300 font-semibold flex items-center"
            >
              View All 
            </Link>
          </div>

          {trendingLoading ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {[...Array(3)].map((_, i) => (
                <div key={i} className="bg-secondary-50 dark:bg-secondary-900 rounded-lg p-4 animate-pulse">
                  <div className="h-48 bg-secondary-200 dark:bg-secondary-700 rounded-lg mb-4"></div>
                  <div className="h-4 bg-secondary-200 dark:bg-secondary-700 rounded mb-2"></div>
                  <div className="h-3 bg-secondary-200 dark:bg-secondary-700 rounded w-2/3"></div>
                </div>
              ))}
            </div>
          ) : trendingModels && trendingModels.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {trendingModels.map((model) => (
                <ModelCard key={model.id} model={model} compact />
              ))}
            </div>
          ) : (
            <div className="text-center py-12">
              <TrendingUp className="w-16 h-16 text-secondary-400 dark:text-secondary-500 mx-auto mb-4" />
              <h3 className="text-xl font-semibold text-secondary-600 dark:text-secondary-400 mb-2">
                No trending models yet
              </h3>
              <p className="text-secondary-500 dark:text-secondary-500 mb-6">
                Popular models will appear here as users interact with them.
              </p>
            </div>
          )}
        </div>
      </section>
    </div>
  )
}

export default Home
