import { useState, useEffect, useRef } from 'react'
import { useNavigate, useParams } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { supabase } from '../lib/supabase'
import { 
  Edit3, 
  Save, 
  ArrowLeft, 
  AlertCircle, 
  CheckCircle, 
  Loader2,
  Upload,
  Camera,
  FileText,
  Tag,
  Grid3X3,
  Eye
} from 'lucide-react'
import toast from 'react-hot-toast'

const ModelEdit = () => {
  const { user } = useAuth()
  const navigate = useNavigate()
  const { modelId } = useParams()
  
  const [model, setModel] = useState(null)
  const [loading, setLoading] = useState(true)
  const [saving, setSaving] = useState(false)
  const [errors, setErrors] = useState({})
  
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    category: '',
    tags: ''
  })
  
  const [newThumbnail, setNewThumbnail] = useState(null)
  const [newPreview, setNewPreview] = useState(null)
  const [thumbnailPreview, setThumbnailPreview] = useState('')
  const [previewImage, setPreviewImage] = useState('')
  
  const thumbnailInputRef = useRef(null)
  const previewInputRef = useRef(null)

  // Load model data
  useEffect(() => {
    if (modelId && user) {
      fetchModel()
    }
  }, [modelId, user])

  const fetchModel = async () => {
    try {
      setLoading(true)
      const { data, error } = await supabase
        .from('models')
        .select('*')
        .eq('id', modelId)
        .eq('user_id', user.id)
        .single()

      if (error) throw error

      if (!data) {
        toast.error('Model not found or you do not have permission to edit it')
        navigate('/models')
        return
      }

      setModel(data)
      setFormData({
        title: data.title || '',
        description: data.description || '',
        category: data.category || '',
        tags: data.tags || ''
      })
      
      if (data.thumbnail_url) {
        setThumbnailPreview(data.thumbnail_url)
      }
      if (data.preview_url) {
        setPreviewImage(data.preview_url)
      }

      console.log(' Model loaded for editing:', data)
    } catch (error) {
      console.error(' Error fetching model:', error)
      toast.error('Failed to load model')
      navigate('/models')
    } finally {
      setLoading(false)
    }
  }

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: value
    }))
    setErrors(prev => ({ ...prev, [name]: null }))
  }

  // Handle thumbnail selection
  const handleThumbnailSelect = (file) => {
    if (file) {
      if (file.size > 5 * 1024 * 1024) { // 5MB limit
        toast.error('Thumbnail image must be less than 5MB')
        return
      }
      
      const allowedTypes = ['image/jpeg', 'image/png', 'image/webp']
      if (!allowedTypes.includes(file.type)) {
        toast.error('Please select a JPEG, PNG, or WebP image')
        return
      }
      
      setNewThumbnail(file)
      setThumbnailPreview(URL.createObjectURL(file))
      setErrors(prev => ({ ...prev, thumbnail: null }))
    }
  }

  // Handle preview image selection
  const handlePreviewSelect = (file) => {
    if (file) {
      if (file.size > 10 * 1024 * 1024) { // 10MB limit
        toast.error('Preview image must be less than 10MB')
        return
      }
      
      const allowedTypes = ['image/jpeg', 'image/png', 'image/webp']
      if (!allowedTypes.includes(file.type)) {
        toast.error('Please select a JPEG, PNG, or WebP image')
        return
      }
      
      setNewPreview(file)
      setPreviewImage(URL.createObjectURL(file))
      setErrors(prev => ({ ...prev, preview: null }))
    }
  }

  // Validate form
  const validateForm = () => {
    const newErrors = {}
    
    if (!formData.title.trim()) {
      newErrors.title = 'Title is required'
    } else if (formData.title.length < 3) {
      newErrors.title = 'Title must be at least 3 characters'
    } else if (formData.title.length > 100) {
      newErrors.title = 'Title must be less than 100 characters'
    }
    
    if (!formData.description.trim()) {
      newErrors.description = 'Description is required'
    } else if (formData.description.length < 10) {
      newErrors.description = 'Description must be at least 10 characters'
    } else if (formData.description.length > 1000) {
      newErrors.description = 'Description must be less than 1000 characters'
    }
    
    if (!formData.category.trim()) {
      newErrors.category = 'Category is required'
    }
    
    if (formData.tags && formData.tags.length > 200) {
      newErrors.tags = 'Tags must be less than 200 characters'
    }
    
    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault()
    
    if (!validateForm()) {
      toast.error('Please fix the errors before saving')
      return
    }
    
    setSaving(true)
    
    try {
      let thumbnailUrl = model?.thumbnail_url || null
      let previewUrl = model?.preview_url || null
      
      // Upload new thumbnail if selected
      if (newThumbnail) {
        console.log(' Uploading new thumbnail...')
        const thumbnailFileName = `thumbnail_${modelId}_${Date.now()}.${newThumbnail.name.split('.').pop()}`
        
        const { data: thumbnailData, error: thumbnailError } = await supabase.storage
          .from('thumbnails')
          .upload(`${user.id}/${thumbnailFileName}`, newThumbnail)
        
        if (thumbnailError) throw thumbnailError
        
        const { data: { publicUrl } } = supabase.storage
          .from('thumbnails')
          .getPublicUrl(`${user.id}/${thumbnailFileName}`)
        
        thumbnailUrl = publicUrl
        console.log(' Thumbnail upload successful')
      }
      
      // Upload new preview if selected
      if (newPreview) {
        console.log(' Uploading new preview...')
        const previewFileName = `preview_${modelId}_${Date.now()}.${newPreview.name.split('.').pop()}`
        
        const { data: previewData, error: previewError } = await supabase.storage
          .from('previews')
          .upload(`${user.id}/${previewFileName}`, newPreview)
        
        if (previewError) throw previewError
        
        const { data: { publicUrl } } = supabase.storage
          .from('previews')
          .getPublicUrl(`${user.id}/${previewFileName}`)
        
        previewUrl = publicUrl
        console.log(' Preview upload successful')
      }
      
      // Update model in database
      console.log(' Updating model...')
      const { data, error } = await supabase
        .from('models')
        .update({
          title: formData.title,
          description: formData.description,
          category: formData.category,
          tags: formData.tags,
          thumbnail_url: thumbnailUrl,
          preview_url: previewUrl,
          updated_at: new Date().toISOString()
        })
        .eq('id', modelId)
        .eq('user_id', user.id)
        .select()
        .single()
      
      if (error) throw error
      
      console.log(' Model updated successfully')
      toast.success('Model updated successfully!')
      
      // Navigate to model view
      navigate(`/models/${modelId}`)
      
    } catch (error) {
      console.error(' Model update failed:', error)
      toast.error(error.message || 'Failed to update model')
    } finally {
      setSaving(false)
    }
  }

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-4">
            Authentication Required
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-6">
            You need to be logged in to edit models.
          </p>
          <button
            onClick={() => navigate('/login')}
            className="btn-primary"
          >
            Sign In
          </button>
        </div>
      </div>
    )
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <Loader2 className="w-8 h-8 animate-spin text-primary-600 mx-auto mb-4" />
          <span className="text-secondary-600 dark:text-secondary-400">Loading model...</span>
        </div>
      </div>
    )
  }

  if (!model) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-4">
            Model Not Found
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-6">
            The model you're looking for doesn't exist or you don't have permission to edit it.
          </p>
          <button
            onClick={() => navigate('/models')}
            className="btn-primary"
          >
            Back to Models
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-4xl mx-auto">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center space-x-4 mb-4">
          <button
            onClick={() => navigate(-1)}
            className="btn-secondary"
          >
            <ArrowLeft className="w-4 h-4 mr-2" />
            Back
          </button>
          <h1 className="text-3xl font-bold text-secondary-900 dark:text-white">
            Edit Model
          </h1>
        </div>
        <p className="text-secondary-600 dark:text-secondary-400">
          Update your 3D model details, images, and metadata.
        </p>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* Model Information */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4 flex items-center">
            <FileText className="w-5 h-5 mr-2" />
            Model Information
          </h2>
          
          <div className="space-y-6">
            {/* Title */}
            <div>
              <label htmlFor="title" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Title *
              </label>
              <input
                type="text"
                id="title"
                name="title"
                value={formData.title}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                placeholder="Enter model title"
              />
              {errors.title && (
                <p className="mt-1 text-sm text-red-600 flex items-center">
                  <AlertCircle className="w-4 h-4 mr-1" />
                  {errors.title}
                </p>
              )}
            </div>

            {/* Description */}
            <div>
              <label htmlFor="description" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Description *
              </label>
              <textarea
                id="description"
                name="description"
                rows="4"
                value={formData.description}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                placeholder="Describe your 3D model, its features, and intended use..."
              />
              <p className="mt-1 text-sm text-secondary-500">
                {formData.description.length}/1000 characters
              </p>
              {errors.description && (
                <p className="mt-1 text-sm text-red-600 flex items-center">
                  <AlertCircle className="w-4 h-4 mr-1" />
                  {errors.description}
                </p>
              )}
            </div>

            {/* Category */}
            <div>
              <label htmlFor="category" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Category *
              </label>
              <select
                id="category"
                name="category"
                value={formData.category}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
              >
                <option value="">Select a category</option>
                <option value="Characters">Characters</option>
                <option value="Vehicles">Vehicles</option>
                <option value="Buildings">Buildings</option>
                <option value="Props">Props</option>
                <option value="Environment">Environment</option>
                <option value="Weapons">Weapons</option>
                <option value="Animals">Animals</option>
                <option value="Furniture">Furniture</option>
                <option value="Machinery">Machinery</option>
                <option value="Other">Other</option>
              </select>
              {errors.category && (
                <p className="mt-1 text-sm text-red-600 flex items-center">
                  <AlertCircle className="w-4 h-4 mr-1" />
                  {errors.category}
                </p>
              )}
            </div>

            {/* Tags */}
            <div>
              <label htmlFor="tags" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Tags
              </label>
              <input
                type="text"
                id="tags"
                name="tags"
                value={formData.tags}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                placeholder="3d,model,game,realistic,low-poly (comma separated)"
              />
              <p className="mt-1 text-sm text-secondary-500">
                Separate tags with commas. {formData.tags.length}/200 characters
              </p>
              {errors.tags && (
                <p className="mt-1 text-sm text-red-600 flex items-center">
                  <AlertCircle className="w-4 h-4 mr-1" />
                  {errors.tags}
                </p>
              )}
            </div>
          </div>
        </div>

        {/* Images Section */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4 flex items-center">
            <Camera className="w-5 h-5 mr-2" />
            Images
          </h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Thumbnail */}
            <div>
              <h3 className="text-lg font-medium text-secondary-900 dark:text-white mb-3">Thumbnail</h3>
              <div className="space-y-3">
                <div className="w-full aspect-video rounded-lg overflow-hidden bg-secondary-200 dark:bg-secondary-700">
                  {thumbnailPreview ? (
                    <img 
                      src={thumbnailPreview} 
                      alt="Thumbnail preview" 
                      className="w-full h-full object-cover"
                    />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center">
                      <Camera className="w-12 h-12 text-secondary-400" />
                    </div>
                  )}
                </div>
                <button
                  type="button"
                  onClick={() => thumbnailInputRef.current?.click()}
                  className="btn-secondary w-full"
                >
                  <Upload className="w-4 h-4 mr-2" />
                  Update Thumbnail
                </button>
                <input
                  ref={thumbnailInputRef}
                  type="file"
                  accept="image/*"
                  onChange={(e) => handleThumbnailSelect(e.target.files?.[0])}
                  className="hidden"
                />
                <p className="text-xs text-secondary-500 text-center">
                  Recommended: 16:9 ratio, max 5MB
                </p>
              </div>
            </div>

            {/* Preview Image */}
            <div>
              <h3 className="text-lg font-medium text-secondary-900 dark:text-white mb-3">Preview Image</h3>
              <div className="space-y-3">
                <div className="w-full aspect-video rounded-lg overflow-hidden bg-secondary-200 dark:bg-secondary-700">
                  {previewImage ? (
                    <img 
                      src={previewImage} 
                      alt="Preview image" 
                      className="w-full h-full object-cover"
                    />
                  ) : (
                    <div className="w-full h-full flex items-center justify-center">
                      <Eye className="w-12 h-12 text-secondary-400" />
                    </div>
                  )}
                </div>
                <button
                  type="button"
                  onClick={() => previewInputRef.current?.click()}
                  className="btn-secondary w-full"
                >
                  <Upload className="w-4 h-4 mr-2" />
                  Update Preview
                </button>
                <input
                  ref={previewInputRef}
                  type="file"
                  accept="image/*"
                  onChange={(e) => handlePreviewSelect(e.target.files?.[0])}
                  className="hidden"
                />
                <p className="text-xs text-secondary-500 text-center">
                  High-quality preview, max 10MB
                </p>
              </div>
            </div>
          </div>
        </div>

        {/* Model File Info */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4 flex items-center">
            <Grid3X3 className="w-5 h-5 mr-2" />
            Model File Information
          </h2>
          
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="text-center p-4 bg-secondary-50 dark:bg-secondary-700 rounded-lg">
              <p className="text-sm text-secondary-600 dark:text-secondary-400">File Size</p>
              <p className="text-lg font-semibold text-secondary-900 dark:text-white">
                {model.file_size ? `${(model.file_size / 1024 / 1024).toFixed(2)} MB` : 'Unknown'}
              </p>
            </div>
            <div className="text-center p-4 bg-secondary-50 dark:bg-secondary-700 rounded-lg">
              <p className="text-sm text-secondary-600 dark:text-secondary-400">File Type</p>
              <p className="text-lg font-semibold text-secondary-900 dark:text-white">
                {model.file_type?.toUpperCase() || 'Unknown'}
              </p>
            </div>
            <div className="text-center p-4 bg-secondary-50 dark:bg-secondary-700 rounded-lg">
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Upload Date</p>
              <p className="text-lg font-semibold text-secondary-900 dark:text-white">
                {new Date(model.created_at).toLocaleDateString()}
              </p>
            </div>
          </div>
          
          <p className="text-sm text-secondary-500 text-center mt-4">
            The 3D model file cannot be changed. Contact support if you need to replace the file.
          </p>
        </div>

        {/* Submit Buttons */}
        <div className="flex justify-end space-x-4">
          <button
            type="button"
            onClick={() => navigate(-1)}
            className="btn-secondary"
            disabled={saving}
          >
            Cancel
          </button>
          <button
            type="submit"
            className="btn-primary"
            disabled={saving}
          >
            {saving ? (
              <>
                <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                Saving...
              </>
            ) : (
              <>
                <Save className="w-4 h-4 mr-2" />
                Save Changes
              </>
            )}
          </button>
        </div>
      </form>
    </div>
  )
}

export default ModelEdit
