import { createContext, useContext, useState, useEffect } from 'react'
import { supabase } from '../lib/supabase'
import toast from 'react-hot-toast'

const AuthContext = createContext()

export const useAuth = () => {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider')
  }
  return context
}

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null)
  const [profile, setProfile] = useState(null)
  const [loading, setLoading] = useState(true)
  const [profileFetching, setProfileFetching] = useState(false) // Prevent repeated fetches

  // Initialize auth state and listen for changes
  useEffect(() => {
    // Simple timeout to prevent infinite loading
    let loadingTimeout = setTimeout(() => {
      console.log(' AuthContext: Loading timeout reached, forcing loading to false')
      setLoading(false)
    }, 5000) // Increased to 5 seconds
    
    // Get initial session
    const getInitialSession = async () => {
      try {
        console.log(' AuthContext: Getting initial session...')
        const { data: { session }, error } = await supabase.auth.getSession()
        
        if (error) {
          console.error(' AuthContext: Error getting session:', error)
          setLoading(false)
          clearTimeout(loadingTimeout)
          return
        }
        
        if (session?.user) {
          console.log(' AuthContext: Session found, user:', session.user.id)
          setUser(session.user)
          try {
            await fetchUserProfile(session.user.id)
          } catch (profileError) {
            console.error(' AuthContext: Error fetching profile:', profileError)
          }
        } else {
          console.log(' AuthContext: No session found')
        }
      } catch (error) {
        console.error(' AuthContext: Error getting initial session:', error)
      } finally {
        console.log(' AuthContext: Initial session check complete, setting loading to false')
        setLoading(false)
        clearTimeout(loadingTimeout)
      }
    }

    // Listen for auth changes
    const { data: { subscription } } = supabase.auth.onAuthStateChange(
      async (event, session) => {
        console.log(' AuthContext: Auth state change:', event, session?.user?.id)
        try {
          if (event === 'SIGNED_IN' && session?.user) {
            setUser(session.user)
            try {
              await fetchUserProfile(session.user.id)
            } catch (profileError) {
              console.error(' AuthContext: Error fetching profile on sign in:', profileError)
            }
          } else if (event === 'SIGNED_OUT') {
            setUser(null)
            setProfile(null)
          } else if (event === 'TOKEN_REFRESHED' && session?.user) {
            setUser(session.user)
          }
        } catch (error) {
          console.error(' AuthContext: Error in auth state change handler:', error)
        } finally {
          setLoading(false)
          clearTimeout(loadingTimeout)
        }
      }
    )

    // Get initial session
    getInitialSession()

    // Cleanup subscription and timeout
    return () => {
      subscription.unsubscribe()
      clearTimeout(loadingTimeout)
    }
  }, [])

  // Check if profiles table exists
  const checkProfilesTable = async () => {
    try {
      console.log(' AuthContext: Checking if profiles table exists...')
      
      // Try to get table info
      const { data, error } = await supabase
        .from('profiles')
        .select('*')
        .limit(0) // Just check structure, no data
      
      if (error) {
        console.error(' AuthContext: Profiles table check failed:', error)
        return false
      }
      
      console.log(' AuthContext: Profiles table exists and is accessible')
      return true
    } catch (error) {
      console.error(' AuthContext: Exception checking profiles table:', error)
      return false
    }
  }

  const fetchUserProfile = async (userId) => {
    // Prevent repeated profile fetching
    if (profileFetching) {
      console.log(' AuthContext: Profile fetch already in progress, skipping...')
      return
    }

    // Add timeout to prevent hanging
    const profileTimeout = setTimeout(() => {
      console.log(' AuthContext: Profile fetch timeout reached, forcing completion')
      setProfileFetching(false)
    }, 15000) // 15 second timeout for profile fetch

    try {
      setProfileFetching(true)
      console.log(' AuthContext: Starting profile fetch for user:', userId)
      
      // First check if profiles table exists
      const tableExists = await checkProfilesTable()
      if (!tableExists) {
        console.error(' AuthContext: Profiles table does not exist or is not accessible')
        clearTimeout(profileTimeout)
        return
      }
      
      // Test Supabase connection with a simple query first
      console.log(' AuthContext: Testing Supabase connection...')
      try {
        const { data: testData, error: testError } = await supabase
          .from('profiles')
          .select('id')
          .limit(1)
        
        if (testError) {
          console.error(' AuthContext: Supabase connection test failed:', testError)
          console.log(' AuthContext: Error details:', {
            code: testError.code,
            message: testError.message,
            details: testError.details
          })
          clearTimeout(profileTimeout)
          return
        }
        console.log(' AuthContext: Supabase connection test successful, test data:', testData)
      } catch (testError) {
        console.error(' AuthContext: Supabase connection test exception:', testError)
        clearTimeout(profileTimeout)
        return
      }
      
      // Now fetch the actual profile
      console.log(' AuthContext: Fetching profile from profiles table...')
      const { data, error } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', userId)
        .single()

      console.log(' AuthContext: Profile query result:', { 
        data, 
        error,
        errorCode: error?.code,
        errorMessage: error?.message,
        hasData: !!data,
        dataKeys: data ? Object.keys(data) : null
      })

      if (error) {
        console.log(' AuthContext: Database error details:', {
          code: error.code,
          message: error.message,
          details: error.details,
          hint: error.hint
        })
        
        if (error.code === 'PGRST116') {
          console.log(' AuthContext: No profile found for user, creating one...')
          // Create a basic profile if none exists
          const { data: newProfile, error: createError } = await supabase
            .from('profiles')
            .insert([
              {
                id: userId,
                username: `user_${userId.slice(0, 8)}`,
                bio: '',
                role: 'creator'
              }
            ])
            .select()
            .single()

          if (createError) {
            console.error(' AuthContext: Error creating profile:', createError)
            clearTimeout(profileTimeout)
            return
          }

          setProfile(newProfile)
          console.log(' AuthContext: Profile created successfully:', newProfile)
        } else {
          console.error(' AuthContext: Error fetching profile:', error)
        }
      } else {
        setProfile(data)
        console.log(' AuthContext: Profile loaded successfully:', data)
      }
    } catch (error) {
      console.error(' AuthContext: Unexpected error in fetchUserProfile:', error)
    } finally {
      clearTimeout(profileTimeout)
      setProfileFetching(false)
      console.log(' AuthContext: Profile fetch process complete')
    }
  }

  const signup = async (email, password, username) => {
    try {
      setLoading(true)
      
      // Check if username is already taken
      const { data: existingUser, error: checkError } = await supabase
        .from('profiles')
        .select('username')
        .eq('username', username)
        .single()

      if (existingUser) {
        throw new Error('Username already taken')
      }

      // Create user account
      const { data, error } = await supabase.auth.signUp({
        email,
        password,
        options: {
          data: {
            username: username
          }
        }
      })

      if (error) throw error

      if (data.user) {
        // Create profile
        const { error: profileError } = await supabase
          .from('profiles')
          .insert([
            {
              id: data.user.id,
              username: username,
              bio: '',
              role: 'creator'
            }
          ])

        if (profileError) {
          console.error('Profile creation error:', profileError)
          // Don't throw here as the user account was created successfully
        }

        toast.success('Account created successfully! Please check your email to verify your account.')
        return { success: true, message: 'Account created successfully!' }
      }

      return { success: true, message: 'Please check your email to verify your account.' }
    } catch (error) {
      console.error('Signup error:', error)
      const message = error.message === 'Username already taken' 
        ? 'Username already taken' 
        : 'Failed to create account'
      toast.error(message)
      return { success: false, message }
    } finally {
      setLoading(false)
    }
  }

  const login = async (email, password) => {
    // Add timeout to prevent hanging
    const loginTimeout = setTimeout(() => {
      console.log(' AuthContext: Login timeout reached, forcing loading to false')
      setLoading(false)
    }, 10000) // 10 second timeout for login

    try {
      console.log(' AuthContext: Starting login process...')
      setLoading(true)
      
      const { data, error } = await supabase.auth.signInWithPassword({
        email,
        password
      })

      if (error) {
        console.error(' AuthContext: Login error:', error)
        throw error
      }

      if (data.user) {
        console.log(' AuthContext: Login successful, user:', data.user.id)
        setUser(data.user)
        
        try {
          console.log(' AuthContext: Fetching profile after login...')
          await fetchUserProfile(data.user.id)
        } catch (profileError) {
          console.error(' AuthContext: Error fetching profile after login:', profileError)
          // Don't fail login if profile fetch fails
        }
        
        toast.success('Welcome back!')
        return { success: true }
      } else {
        console.error(' AuthContext: No user data in login response')
        throw new Error('No user data received')
      }
    } catch (error) {
      console.error(' AuthContext: Login error:', error)
      toast.error('Invalid email or password')
      return { success: false, message: error.message }
    } finally {
      console.log(' AuthContext: Login process complete, setting loading to false')
      clearTimeout(loginTimeout)
      setLoading(false)
    }
  }

  const logout = async () => {
    try {
      console.log(' AuthContext: Starting logout process...')
      
      // Don't set loading to true for logout to avoid timeout interference
      const { error } = await supabase.auth.signOut()
      if (error) {
        console.error(' AuthContext: Supabase logout error:', error)
        throw error
      }
      
      console.log(' AuthContext: Supabase logout successful, clearing local state...')
      setUser(null)
      setProfile(null)
      setLoading(false) // Ensure loading is false after logout
      
      console.log(' AuthContext: Logout completed successfully')
      toast.success('Signed out successfully')
    } catch (error) {
      console.error(' AuthContext: Logout error:', error)
      toast.error('Failed to sign out')
      setLoading(false) // Ensure loading is false even on error
    }
  }

  // Force logout that bypasses loading states
  const forceLogout = async () => {
    try {
      console.log(' AuthContext: Force logout - bypassing loading states...')
      
      // Clear all states immediately
      setUser(null)
      setProfile(null)
      setLoading(false)
      
      // Then attempt Supabase logout
      const { error } = await supabase.auth.signOut()
      if (error) {
        console.error(' AuthContext: Supabase logout error (but states cleared):', error)
      }
      
      console.log(' AuthContext: Force logout completed')
      toast.success('Signed out successfully')
    } catch (error) {
      console.error(' AuthContext: Force logout error:', error)
      // States are already cleared, so we're good
    }
  }

  const updateProfile = async (profileData) => {
    try {
      if (!user) throw new Error('No user logged in')

      const { data, error } = await supabase
        .from('profiles')
        .update(profileData)
        .eq('id', user.id)
        .select()
        .single()

      if (error) throw error

      setProfile(data)
      toast.success('Profile updated successfully!')
      return { success: true, data }
    } catch (error) {
      console.error('Profile update error:', error)
      toast.error('Failed to update profile')
      return { success: false, message: error.message }
    }
  }

  const forgotPassword = async (email) => {
    try {
      const { error } = await supabase.auth.resetPasswordForEmail(email, {
        redirectTo: `${window.location.origin}/reset-password`
      })

      if (error) throw error

      toast.success('Password reset email sent!')
      return { success: true }
    } catch (error) {
      console.error('Forgot password error:', error)
      toast.error('Failed to send reset email')
      return { success: false, message: error.message }
    }
  }

  const changePassword = async (newPassword) => {
    try {
      const { error } = await supabase.auth.updateUser({
        password: newPassword
      })

      if (error) throw error

      toast.success('Password updated successfully!')
      return { success: true }
    } catch (error) {
      console.error('Change password error:', error)
      toast.error('Failed to update password')
      return { success: false, message: error.message }
    }
  }

  // Reset loading state
  const resetLoading = () => {
    console.log(' AuthContext: Manually resetting loading state')
    setLoading(false)
    setProfileFetching(false)
  }

  // Force reset all states (emergency reset)
  const forceReset = () => {
    console.log(' AuthContext: Force resetting all states')
    setLoading(false)
    setProfileFetching(false)
    setProfile(null)
    // Don't reset user as that might cause auth issues
  }

  // Reset profile state to allow fresh fetching
  const resetProfile = () => {
    setProfile(null)
    setProfileFetching(false)
    console.log(' AuthContext: Profile state reset')
  }

  // Manual profile fetch for testing
  const manualFetchProfile = async () => {
    if (!user) {
      console.log(' AuthContext: No user to fetch profile for')
      return
    }
    
    console.log(' AuthContext: Manual profile fetch triggered')
    await fetchUserProfile(user.id)
  }

  // Simple database connectivity test
  const testBasicConnection = async () => {
    // Add timeout to prevent hanging
    const testTimeout = setTimeout(() => {
      console.log(' AuthContext: Basic connection test timeout reached')
    }, 10000) // 10 second timeout

    try {
      console.log(' AuthContext: Testing basic Supabase connectivity...')
      
      // Test 1: Basic auth connection
      console.log(' Test 1: Testing auth connection...')
      const { data: authData, error: authError } = await supabase.auth.getSession()
      if (authError) {
        console.error(' Test 1 Failed: Auth error:', authError)
        clearTimeout(testTimeout)
        return false
      }
      console.log(' Test 1 Passed: Auth connection successful')
      
      // Test 2: Test if we can make any database query
      console.log(' Test 2: Testing basic database access...')
      try {
        // Try to query a system table or any accessible table
        const { data: testData, error: testError } = await supabase
          .from('profiles')
          .select('id')
          .limit(1)
        
        if (testError) {
          console.error(' Test 2 Failed: Database query error:', testError)
          console.log(' Error details:', {
            code: testError.code,
            message: testError.message,
            details: testError.details,
            hint: testError.hint
          })
          
          // Check if it's a table not found error
          if (testError.code === 'PGRST116') {
            console.log(' Table not found - this suggests the profiles table is missing')
          } else if (testError.code === '42501') {
            console.log(' Permission denied - this suggests RLS policies are blocking access')
          }
          
          clearTimeout(testTimeout)
          return false
        }
        
        console.log(' Test 2 Passed: Database query successful, data:', testData)
        clearTimeout(testTimeout)
        return true
        
      } catch (dbException) {
        console.error(' Test 2 Failed: Database exception:', dbException)
        clearTimeout(testTimeout)
        return false
      }
      
    } catch (error) {
      console.error(' Basic connection test failed:', error)
      clearTimeout(testTimeout)
      return false
    }
  }

  // Test basic Supabase client connection
  const testSupabaseConnection = async () => {
    try {
      console.log(' AuthContext: Testing basic Supabase client connection...')
      
      // Test 1: Check if supabase client is properly initialized
      console.log(' Test 1: Checking Supabase client...')
      if (!supabase) {
        console.error(' Test 1 Failed: Supabase client is null/undefined')
        return false
      }
      console.log(' Test 1 Passed: Supabase client exists')
      
      // Test 2: Check environment variables
      console.log(' Test 2: Checking environment variables...')
      const supabaseUrl = import.meta.env.VITE_SUPABASE_URL
      const supabaseKey = import.meta.env.VITE_SUPABASE_ANON_KEY
      
      if (!supabaseUrl) {
        console.error(' Test 2 Failed: VITE_SUPABASE_URL is missing')
        return false
      }
      if (!supabaseKey) {
        console.error(' Test 2 Failed: VITE_SUPABASE_ANON_KEY is missing')
        return false
      }
      console.log(' Test 2 Passed: Environment variables are set')
      console.log(' URL length:', supabaseUrl.length)
      console.log(' Key length:', supabaseKey.length)
      
      // Test 3: Test basic auth connection (no database)
      console.log(' Test 3: Testing basic auth connection...')
      const { data: authData, error: authError } = await supabase.auth.getSession()
      if (authError) {
        console.error(' Test 3 Failed: Auth connection error:', authError)
        return false
      }
      console.log(' Test 3 Passed: Auth connection successful')
      
      console.log(' All Supabase connection tests passed!')
      return true
      
    } catch (error) {
      console.error(' Supabase connection test failed:', error)
      return false
    }
  }

  // Simple database diagnostic function
  const diagnoseDatabase = async () => {
    try {
      console.log(' AuthContext: Starting Supabase connection diagnosis...')
      
      const result = await testSupabaseConnection()
      
      if (result) {
        console.log(' Supabase connection: All tests passed')
        console.log(' Now testing database access...')
        
        // If connection is good, test database access
        const dbResult = await testBasicConnection()
        if (dbResult) {
          console.log(' Database access: All tests passed')
        } else {
          console.log(' Database access: Tests failed - connection works but database has issues')
        }
      } else {
        console.log(' Supabase connection: Tests failed - connection issue detected')
      }
      
      console.log(' Supabase diagnosis complete')
      
    } catch (error) {
      console.error(' Supabase diagnosis failed:', error)
    }
  }

  const value = {
    user,
    profile,
    loading,
    isAuthenticated: !!user,
    isCreator: (profile?.role || '').toLowerCase() === 'creator',
    signup,
    login,
    logout,
    forceLogout,
    fetchUserProfile,
    manualFetchProfile,
    diagnoseDatabase,
    resetProfile,
    resetLoading,
    forceReset,
    debugAuth: async () => {
      console.log(' Debug: Current auth state:', { user, profile, loading })
      try {
        const { data: { session } } = await supabase.auth.getSession()
        console.log(' Debug: Current session:', session)
        const { data: { user: currentUser } } = await supabase.auth.getUser()
        console.log(' Debug: Current user:', currentUser)

        if (currentUser) {
          console.log(' Debug: Testing database connection...')
          
          // Test if profiles table exists and show its structure
          const { data: tableInfo, error: tableError } = await supabase
            .from('profiles')
            .select('*')
            .limit(1)
          
          console.log(' Debug: Profiles table test:', { tableInfo, tableError })
          
          // Try to get the current user's profile
          const { data: profileData, error: profileError } = await supabase
            .from('profiles')
            .select('*')
            .eq('id', currentUser.id)
            .single()
          console.log(' Debug: Profile query result:', { profileData, profileError })

          // Test table count
          const { count, error: countError } = await supabase
            .from('profiles')
            .select('*', { count: 'exact', head: true })
          console.log(' Debug: Table count result:', { count, countError })
        }
      } catch (error) {
        console.error(' Debug: Error getting auth info:', error)
      }
    }
  }

  return (
    <AuthContext.Provider value={value}>
      {children}
    </AuthContext.Provider>
  )
}
