import { supabase } from '../lib/supabase';

export const downloadService = {
  // Download a model file
  async downloadModel(modelId, modelData) {
    try {
      // Get user's IP address
      const ipResponse = await fetch('https://api.ipify.org?format=json');
      const ipData = await ipResponse.json();
      
      // Record the download in Supabase
      const { error: downloadError } = await supabase
        .from('downloads')
        .insert({
          model_id: modelId,
          user_id: (await supabase.auth.getUser()).data.user?.id,
          ip_address: ipData.ip,
          user_agent: navigator.userAgent
        });

      if (downloadError) {
        console.error('Error recording download:', downloadError);
        // Don't fail the download if tracking fails
      }

      // Get the file from Supabase Storage
      const { data: fileData, error: fileError } = await supabase.storage
        .from('models')
        .download(modelData.file_url);

      if (fileError) {
        throw new Error('Failed to download file from storage');
      }

      // Create download link
      const url = window.URL.createObjectURL(fileData);
      const a = document.createElement('a');
      a.href = url;
      a.download = `${modelData.title}.${modelData.file_type || 'zip'}`;
      document.body.appendChild(a);
      a.click();
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);

      return { success: true };
    } catch (error) {
      console.error('Download error:', error);
      throw error;
    }
  },

  // Get download statistics for a model
  async getDownloadStats(modelId) {
    try {
      const { data: downloads, error } = await supabase
        .from('downloads')
        .select('*')
        .eq('model_id', modelId);

      if (error) {
        throw error;
      }

      const now = new Date();
      const today = new Date(now.getFullYear(), now.getMonth(), now.getDate());
      const weekAgo = new Date(today.getTime() - 7 * 24 * 60 * 60 * 1000);
      const monthAgo = new Date(today.getTime() - 30 * 24 * 60 * 60 * 1000);

      return {
        total: downloads.length,
        today: downloads.filter(d => new Date(d.downloaded_at) >= today).length,
        thisWeek: downloads.filter(d => new Date(d.downloaded_at) >= weekAgo).length,
        thisMonth: downloads.filter(d => new Date(d.downloaded_at) >= monthAgo).length
      };
    } catch (error) {
      console.error('Error getting download stats:', error);
      throw error;
    }
  },

  // Get user's download history
  async getUserDownloadHistory() {
    try {
      const { data: { user } } = await supabase.auth.getUser();
      
      if (!user) {
        throw new Error('User not authenticated');
      }

      const { data: downloads, error } = await supabase
        .from('downloads')
        .select(`
          *,
          model:models(
            id,
            title,
            description,
            thumbnail_url,
            category,
            file_type,
            file_size
          )
        `)
        .eq('user_id', user.id)
        .order('downloaded_at', { ascending: false });

      if (error) {
        throw error;
      }

      return downloads;
    } catch (error) {
      console.error('Error getting download history:', error);
      throw error;
    }
  }
};
