import { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { supabase } from '../lib/supabase'
import { 
  BarChart3, 
  TrendingUp, 
  Eye, 
  Download, 
  Heart, 
  Calendar,
  Filter,
  ArrowLeft,
  RefreshCw,
  Loader2,
  TrendingDown,
  Minus,
  Activity,
  Users,
  Clock,
  Target
} from 'lucide-react'
import toast from 'react-hot-toast'

const ModelAnalytics = () => {
  const { user } = useAuth()
  const navigate = useNavigate()
  
  const [models, setModels] = useState([])
  const [loading, setLoading] = useState(true)
  const [timeRange, setTimeRange] = useState('30') // 7, 30, 90 days
  const [selectedModel, setSelectedModel] = useState('all')
  const [analytics, setAnalytics] = useState({
    totalViews: 0,
    totalDownloads: 0,
    totalLikes: 0,
    avgViewsPerModel: 0,
    topPerformingModel: null,
    recentActivity: []
  })

  // Load user's models and analytics
  useEffect(() => {
    if (user) {
      fetchUserModels()
    }
  }, [user, timeRange])

  const fetchUserModels = async () => {
    try {
      setLoading(true)
      const { data, error } = await supabase
        .from('models')
        .select(`
          *,
          creator:profiles!models_user_id_fkey(username, avatar_url)
        `)
        .eq('user_id', user.id)
        .order('created_at', { ascending: false })

      if (error) throw error

      setModels(data || [])
      await calculateAnalytics(data || [])
      console.log(' Analytics loaded for models:', data)
    } catch (error) {
      console.error(' Error fetching models:', error)
      toast.error('Failed to load your models')
    } finally {
      setLoading(false)
    }
  }

  const calculateAnalytics = async (modelsData) => {
    if (!modelsData.length) return

    const now = new Date()
    const daysAgo = new Date(now.getTime() - (parseInt(timeRange) * 24 * 60 * 60 * 1000))

    // Calculate basic metrics
    const totalViews = modelsData.reduce((sum, model) => sum + (model.views || 0), 0)
    const totalDownloads = modelsData.reduce((sum, model) => sum + (model.downloads || 0), 0)
    const totalLikes = modelsData.reduce((sum, model) => sum + (model.likes || 0), 0)
    const avgViewsPerModel = totalViews / modelsData.length

    // Find top performing model
    const topPerformingModel = modelsData.reduce((top, model) => {
      const modelScore = (model.views || 0) + ((model.likes || 0) * 2) + ((model.downloads || 0) * 3)
      const topScore = (top.views || 0) + ((top.likes || 0) * 2) + ((top.downloads || 0) * 3)
      return modelScore > topScore ? model : top
    })

    // Calculate recent activity (last 7 days)
    const recentActivity = modelsData
      .filter(model => new Date(model.updated_at || model.created_at) > new Date(now.getTime() - (7 * 24 * 60 * 60 * 1000)))
      .map(model => ({
        id: model.id,
        title: model.title,
        action: model.updated_at ? 'Updated' : 'Created',
        date: new Date(model.updated_at || model.created_at),
        views: model.views || 0,
        likes: model.likes || 0,
        downloads: model.downloads || 0
      }))
      .sort((a, b) => b.date - a.date)
      .slice(0, 5)

    setAnalytics({
      totalViews,
      totalDownloads,
      totalLikes,
      avgViewsPerModel: Math.round(avgViewsPerModel * 100) / 100,
      topPerformingModel,
      recentActivity
    })
  }

  const refreshAnalytics = async () => {
    await fetchUserModels()
    toast.success('Analytics refreshed!')
  }

  const getModelPerformance = (model) => {
    const views = model.views || 0
    const likes = model.likes || 0
    const downloads = model.downloads || 0
    
    // Calculate performance score (weighted)
    const score = views + (likes * 2) + (downloads * 3)
    
    if (score === 0) return { level: 'New', color: 'text-gray-500', bgColor: 'bg-gray-100' }
    if (score < 10) return { level: 'Low', color: 'text-red-500', bgColor: 'bg-red-100' }
    if (score < 50) return { level: 'Medium', color: 'text-yellow-500', bgColor: 'bg-yellow-100' }
    if (score < 100) return { level: 'Good', color: 'text-blue-500', bgColor: 'bg-blue-100' }
    return { level: 'Excellent', color: 'text-green-500', bgColor: 'bg-green-100' }
  }

  const getTrendIcon = (current, previous) => {
    if (current > previous) return <TrendingUp className="w-4 h-4 text-green-500" />
    if (current < previous) return <TrendingDown className="w-4 h-4 text-red-500" />
    return <Minus className="w-4 h-4 text-gray-500" />
  }

  const formatNumber = (num) => {
    if (num >= 1000000) return (num / 1000000).toFixed(1) + 'M'
    if (num >= 1000) return (num / 1000).toFixed(1) + 'K'
    return num.toString()
  }

  if (!user) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-4">
            Authentication Required
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-6">
            You need to be logged in to view analytics.
          </p>
          <button
            onClick={() => navigate('/login')}
            className="btn-primary"
          >
            Sign In
          </button>
        </div>
      </div>
    )
  }

  return (
    <div className="max-w-7xl mx-auto">
      {/* Header */}
      <div className="mb-8">
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => navigate(-1)}
              className="btn-secondary"
            >
              <ArrowLeft className="w-4 h-4 mr-2" />
              Back
            </button>
            <h1 className="text-3xl font-bold text-secondary-900 dark:text-white">
              Model Analytics
            </h1>
          </div>
          
          <button
            onClick={refreshAnalytics}
            className="btn-secondary"
            disabled={loading}
          >
            <RefreshCw className={`w-4 h-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
            Refresh
          </button>
        </div>
        
        <p className="text-secondary-600 dark:text-secondary-400">
          Track the performance of your 3D models and understand what resonates with your audience.
        </p>
      </div>

      {/* Time Range Filter */}
      <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-4 mb-6">
        <div className="flex items-center space-x-4">
          <Calendar className="w-5 h-5 text-secondary-400" />
          <span className="text-sm font-medium text-secondary-700 dark:text-secondary-300">Time Range:</span>
          <select
            value={timeRange}
            onChange={(e) => setTimeRange(e.target.value)}
            className="px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
          >
            <option value="7">Last 7 days</option>
            <option value="30">Last 30 days</option>
            <option value="90">Last 90 days</option>
          </select>
        </div>
      </div>

      {/* Overview Stats */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
        {/* Total Views */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Total Views</p>
              <p className="text-3xl font-bold text-secondary-900 dark:text-white">
                {formatNumber(analytics.totalViews)}
              </p>
            </div>
            <div className="p-3 bg-blue-100 dark:bg-blue-900 rounded-lg">
              <Eye className="w-8 h-8 text-blue-600 dark:text-blue-400" />
            </div>
          </div>
          <div className="mt-4 flex items-center text-sm text-secondary-600 dark:text-secondary-400">
            <Activity className="w-4 h-4 mr-1" />
            {analytics.avgViewsPerModel} avg per model
          </div>
        </div>

        {/* Total Downloads */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Total Downloads</p>
              <p className="text-3xl font-bold text-secondary-900 dark:text-white">
                {formatNumber(analytics.totalDownloads)}
              </p>
            </div>
            <div className="p-3 bg-green-100 dark:bg-green-900 rounded-lg">
              <Download className="w-8 h-8 text-green-600 dark:text-green-400" />
            </div>
          </div>
          <div className="mt-4 flex items-center text-sm text-secondary-600 dark:text-secondary-400">
            <Target className="w-4 h-4 mr-1" />
            {analytics.totalViews > 0 ? Math.round((analytics.totalDownloads / analytics.totalViews) * 100) : 0}% conversion
          </div>
        </div>

        {/* Total Likes */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Total Likes</p>
              <p className="text-3xl font-bold text-secondary-900 dark:text-white">
                {formatNumber(analytics.totalLikes)}
              </p>
            </div>
            <div className="p-3 bg-red-100 dark:bg-red-900 rounded-lg">
              <Heart className="w-8 h-8 text-red-600 dark:text-red-400" />
            </div>
          </div>
          <div className="mt-4 flex items-center text-sm text-secondary-600 dark:text-secondary-400">
            <Users className="w-4 h-4 mr-1" />
            {analytics.totalViews > 0 ? Math.round((analytics.totalLikes / analytics.totalViews) * 100) : 0}% engagement
          </div>
        </div>

        {/* Total Models */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <div className="flex items-center justify-between">
            <div>
              <p className="text-sm text-secondary-600 dark:text-secondary-400">Total Models</p>
              <p className="text-3xl font-bold text-secondary-900 dark:text-white">
                {models.length}
              </p>
            </div>
            <div className="p-3 bg-purple-100 dark:bg-purple-900 rounded-lg">
              <BarChart3 className="w-8 h-8 text-purple-600 dark:text-purple-400" />
            </div>
          </div>
          <div className="mt-4 flex items-center text-sm text-secondary-600 dark:text-secondary-400">
            <Clock className="w-4 h-4 mr-1" />
            {models.length > 0 ? Math.round(analytics.totalViews / models.length) : 0} avg views
          </div>
        </div>
      </div>

      {/* Top Performing Model */}
      {analytics.topPerformingModel && (
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6 mb-8">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4 flex items-center">
            <TrendingUp className="w-5 h-5 mr-2 text-green-500" />
            Top Performing Model
          </h2>
          <div className="flex items-center space-x-6">
            <div className="w-24 h-24 rounded-lg overflow-hidden bg-secondary-200 dark:bg-secondary-700 flex-shrink-0">
              {analytics.topPerformingModel.thumbnail_url ? (
                <img 
                  src={analytics.topPerformingModel.thumbnail_url} 
                  alt={analytics.topPerformingModel.title}
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="w-full h-full flex items-center justify-center">
                  <BarChart3 className="w-12 h-12 text-secondary-400" />
                </div>
              )}
            </div>
            <div className="flex-1">
              <h3 className="text-xl font-semibold text-secondary-900 dark:text-white mb-2">
                {analytics.topPerformingModel.title}
              </h3>
              <p className="text-secondary-600 dark:text-secondary-400 mb-4">
                {analytics.topPerformingModel.description}
              </p>
              <div className="grid grid-cols-3 gap-4">
                <div className="text-center">
                  <div className="text-2xl font-bold text-blue-600">{analytics.topPerformingModel.views || 0}</div>
                  <div className="text-sm text-secondary-500">Views</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-green-600">{analytics.topPerformingModel.downloads || 0}</div>
                  <div className="text-sm text-secondary-500">Downloads</div>
                </div>
                <div className="text-center">
                  <div className="text-2xl font-bold text-red-600">{analytics.topPerformingModel.likes || 0}</div>
                  <div className="text-sm text-secondary-500">Likes</div>
                </div>
              </div>
            </div>
            <button
              onClick={() => navigate(`/models/${analytics.topPerformingModel.id}`)}
              className="btn-primary"
            >
              View Model
            </button>
          </div>
        </div>
      )}

      {/* Recent Activity */}
      {analytics.recentActivity.length > 0 && (
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6 mb-8">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4 flex items-center">
            <Activity className="w-5 h-5 mr-2" />
            Recent Activity
          </h2>
          <div className="space-y-4">
            {analytics.recentActivity.map((activity) => (
              <div key={activity.id} className="flex items-center justify-between p-4 bg-secondary-50 dark:bg-secondary-700 rounded-lg">
                <div className="flex items-center space-x-4">
                  <div className="w-10 h-10 bg-primary-100 dark:bg-primary-900 rounded-full flex items-center justify-center">
                    <Activity className="w-5 h-5 text-primary-600 dark:text-primary-400" />
                  </div>
                  <div>
                    <p className="font-medium text-secondary-900 dark:text-white">
                      {activity.title} was {activity.action.toLowerCase()}
                    </p>
                    <p className="text-sm text-secondary-500">
                      {activity.date.toLocaleDateString()} at {activity.date.toLocaleTimeString()}
                    </p>
                  </div>
                </div>
                <div className="flex items-center space-x-6 text-sm text-secondary-500">
                  <span className="flex items-center">
                    <Eye className="w-4 h-4 mr-1" />
                    {activity.views}
                  </span>
                  <span className="flex items-center">
                    <Heart className="w-4 h-4 mr-1" />
                    {activity.likes}
                  </span>
                  <span className="flex items-center">
                    <Download className="w-4 h-4 mr-1" />
                    {activity.downloads}
                  </span>
                </div>
              </div>
            ))}
          </div>
        </div>
      )}

      {/* Models Performance Table */}
      <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
        <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4 flex items-center">
          <BarChart3 className="w-5 h-5 mr-2" />
          Models Performance
        </h2>
        
        {loading ? (
          <div className="flex items-center justify-center py-12">
            <Loader2 className="w-8 h-8 animate-spin text-primary-600" />
            <span className="ml-2 text-secondary-600 dark:text-secondary-400">Loading analytics...</span>
          </div>
        ) : models.length === 0 ? (
          <div className="text-center py-12">
            <BarChart3 className="w-16 h-16 text-secondary-400 mx-auto mb-4" />
            <h3 className="text-xl font-semibold text-secondary-900 dark:text-white mb-2">
              No models yet
            </h3>
            <p className="text-secondary-600 dark:text-secondary-400 mb-6">
              Upload your first 3D model to start tracking analytics.
            </p>
            <button
              onClick={() => navigate('/upload')}
              className="btn-primary"
            >
              Upload Model
            </button>
          </div>
        ) : (
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead>
                <tr className="border-b border-secondary-200 dark:border-secondary-700">
                  <th className="text-left py-3 px-4 font-medium text-secondary-700 dark:text-secondary-300">Model</th>
                  <th className="text-center py-3 px-4 font-medium text-secondary-700 dark:text-secondary-300">Views</th>
                  <th className="text-center py-3 px-4 font-medium text-secondary-700 dark:text-secondary-300">Downloads</th>
                  <th className="text-center py-3 px-4 font-medium text-secondary-700 dark:text-secondary-300">Likes</th>
                  <th className="text-center py-3 px-4 font-medium text-secondary-700 dark:text-secondary-300">Performance</th>
                  <th className="text-center py-3 px-4 font-medium text-secondary-700 dark:text-secondary-300">Actions</th>
                </tr>
              </thead>
              <tbody>
                {models.map((model) => {
                  const performance = getModelPerformance(model)
                  return (
                    <tr key={model.id} className="border-b border-secondary-100 dark:border-secondary-800 hover:bg-secondary-50 dark:hover:bg-secondary-700">
                      <td className="py-4 px-4">
                        <div className="flex items-center space-x-3">
                          <div className="w-12 h-12 rounded-lg overflow-hidden bg-secondary-200 dark:bg-secondary-700 flex-shrink-0">
                            {model.thumbnail_url ? (
                              <img 
                                src={model.thumbnail_url} 
                                alt={model.title}
                                className="w-full h-full object-cover"
                              />
                            ) : (
                              <div className="w-full h-full flex items-center justify-center">
                                <BarChart3 className="w-6 h-6 text-secondary-400" />
                              </div>
                            )}
                          </div>
                          <div>
                            <p className="font-medium text-secondary-900 dark:text-white">{model.title}</p>
                            <p className="text-sm text-secondary-500">{model.category}</p>
                          </div>
                        </div>
                      </td>
                      <td className="text-center py-4 px-4">
                        <div className="flex items-center justify-center space-x-2">
                          <Eye className="w-4 h-4 text-blue-500" />
                          <span className="font-medium">{model.views || 0}</span>
                        </div>
                      </td>
                      <td className="text-center py-4 px-4">
                        <div className="flex items-center justify-center space-x-2">
                          <Download className="w-4 h-4 text-green-500" />
                          <span className="font-medium">{model.downloads || 0}</span>
                        </div>
                      </td>
                      <td className="text-center py-4 px-4">
                        <div className="flex items-center justify-center space-x-2">
                          <Heart className="w-4 h-4 text-red-500" />
                          <span className="font-medium">{model.likes || 0}</span>
                        </div>
                      </td>
                      <td className="text-center py-4 px-4">
                        <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium ${performance.bgColor} ${performance.color}`}>
                          {performance.level}
                        </span>
                      </td>
                      <td className="text-center py-4 px-4">
                        <div className="flex items-center justify-center space-x-2">
                          <button
                            onClick={() => navigate(`/models/${model.id}`)}
                            className="p-2 text-blue-500 hover:bg-blue-100 dark:hover:bg-blue-900 rounded-lg transition-colors"
                            title="View Model"
                          >
                            <Eye className="w-4 h-4" />
                          </button>
                          <button
                            onClick={() => navigate(`/models/${model.id}/edit`)}
                            className="p-2 text-green-500 hover:bg-green-100 dark:hover:bg-green-900 rounded-lg transition-colors"
                            title="Edit Model"
                          >
                            <BarChart3 className="w-4 h-4" />
                          </button>
                        </div>
                      </td>
                    </tr>
                  )
                })}
              </tbody>
            </table>
          </div>
        )}
      </div>
    </div>
  )
}

export default ModelAnalytics
