import { useState } from 'react'
import { Link } from 'react-router-dom'
import { Download, Heart, Eye, Calendar, User } from 'lucide-react'
import { useAuth } from '../contexts/AuthContext'
import { modelsService } from '../services/api'
import { apiUtils } from '../services/api'
import toast from 'react-hot-toast'

const ModelCard = ({ model, compact = false }) => {
  const [isLiked, setIsLiked] = useState(model.isLiked || false)
  const [likeCount, setLikeCount] = useState(model.likeCount || 0)
  const [isLiking, setIsLiking] = useState(false)
  const { isAuthenticated } = useAuth()

  // Normalize data coming from different backends/mocks
  const creator = model.creator || model.user || {}
  const tags = Array.isArray(model.tags)
    ? model.tags
    : typeof model.tags === 'string'
      ? model.tags.split(',').map((t) => t.trim()).filter(Boolean)
      : []

  const handleLike = async () => {
    if (!isAuthenticated) {
      toast.error('Please sign in to like models')
      return
    }

    try {
      setIsLiking(true)
      const response = await modelsService.like(model.id)
      setIsLiked(response.liked)
      setLikeCount(prev => response.liked ? prev + 1 : prev - 1)
      
      if (response.liked) {
        toast.success('Model added to favorites!')
      } else {
        toast.success('Model removed from favorites')
      }
    } catch (error) {
      toast.error('Failed to update like status')
    } finally {
      setIsLiking(false)
    }
  }

  const handleDownload = () => {
    if (!isAuthenticated) {
      toast.error('Please sign in to download models')
      return
    }
    // Download logic will be implemented
    toast.success('Starting download...')
  }

  if (compact) {
    return (
      <Link
        to={`/models/${model.id}`}
        className="card hover:shadow-lg transition-all duration-200 group"
      >
        <div className="relative">
          {model.thumbnailUrl ? (
            <img
              src={model.thumbnailUrl}
              alt={model.title}
              className="w-full h-32 object-cover rounded-t-lg group-hover:scale-105 transition-transform duration-200"
            />
          ) : (
            <div className="w-full h-32 bg-gradient-to-br from-secondary-100 to-secondary-200 dark:from-secondary-700 dark:to-secondary-800 rounded-t-lg flex items-center justify-center">
              <div className="text-secondary-400 dark:text-secondary-500">
                <svg className="w-12 h-12" fill="currentColor" viewBox="0 0 20 20">
                  <path d="M4 3a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V5a2 2 0 00-2-2H4zm12 12H4l4-8 3 6 2-4 3 6z" />
                </svg>
              </div>
            </div>
          )}
          
          {/* Category badge */}
          <div className="absolute top-2 left-2">
            <span className="badge-primary text-xs px-2 py-1">
              {model.category}
            </span>
          </div>
        </div>

        <div className="card-body p-4">
          <h3 className="font-semibold text-secondary-900 dark:text-white text-sm mb-2 line-clamp-2 group-hover:text-primary-600 dark:group-hover:text-primary-400 transition-colors duration-200">
            {model.title}
          </h3>
          
          <div className="flex items-center justify-between text-xs text-secondary-500 dark:text-secondary-400">
            <div className="flex items-center space-x-1">
              <User className="w-3 h-3" />
              <span>{creator?.username}</span>
            </div>
            <div className="flex items-center space-x-3">
              <span className="flex items-center space-x-1">
                <Eye className="w-3 h-3" />
                <span>{model.viewCount}</span>
              </span>
              <span className="flex items-center space-x-1">
                <Download className="w-3 h-3" />
                <span>{model.downloadCount}</span>
              </span>
            </div>
          </div>
        </div>
      </Link>
    )
  }

  return (
    <div className="card hover:shadow-lg transition-all duration-200 group">
      <div className="relative">
        {model.thumbnailUrl ? (
          <img
            src={model.thumbnailUrl}
            alt={model.title}
            className="w-full h-48 object-cover rounded-t-lg group-hover:scale-105 transition-transform duration-200"
          />
        ) : (
          <div className="w-full h-48 bg-gradient-to-br from-secondary-100 to-secondary-200 dark:from-secondary-700 dark:to-secondary-800 rounded-t-lg flex items-center justify-center">
            <div className="text-secondary-400 dark:text-secondary-500">
              <svg className="w-16 h-16" fill="currentColor" viewBox="0 0 20 20">
                <path d="M4 3a2 2 0 00-2 2v10a2 2 0 002 2h12a2 2 0 002-2V5a2 2 0 00-2-2H4zm12 12H4l4-8 3 6 2-4 3 6z" />
              </svg>
            </div>
          </div>
        )}
        
        {/* Category badge */}
        <div className="absolute top-3 left-3">
          <span className="badge-primary px-3 py-1">
            {model.category}
          </span>
        </div>

        {/* Action buttons overlay */}
        <div className="absolute top-3 right-3 flex space-x-2">
          <button
            onClick={handleLike}
            disabled={isLiking}
            className={`p-2 rounded-full transition-all duration-200 ${
              isLiked
                ? 'bg-red-500 text-white hover:bg-red-600'
                : 'bg-white/90 dark:bg-secondary-800/90 text-secondary-600 dark:text-secondary-400 hover:bg-white dark:hover:bg-secondary-800'
            }`}
            aria-label={isLiked ? 'Unlike model' : 'Like model'}
          >
            <Heart className={`w-4 h-4 ${isLiked ? 'fill-current' : ''}`} />
          </button>
          
          <button
            onClick={handleDownload}
            className="p-2 rounded-full bg-white/90 dark:bg-secondary-800/90 text-secondary-600 dark:text-secondary-400 hover:bg-white dark:hover:bg-secondary-800 transition-all duration-200"
            aria-label="Download model"
          >
            <Download className="w-4 h-4" />
          </button>
        </div>
      </div>

      <div className="card-body">
        <Link to={`/models/${model.id}`}>
          <h3 className="font-semibold text-lg text-secondary-900 dark:text-white mb-2 group-hover:text-primary-600 dark:group-hover:text-primary-400 transition-colors duration-200 line-clamp-2">
            {model.title}
          </h3>
        </Link>
        
        {model.description && (
          <p className="text-secondary-600 dark:text-secondary-400 text-sm mb-4 line-clamp-2">
            {model.description}
          </p>
        )}

        {/* Tags */}
        {tags.length > 0 && (
          <div className="flex flex-wrap gap-2 mb-4">
            {tags.slice(0, 3).map((tag, index) => (
              <span
                key={index}
                className="badge-secondary text-xs px-2 py-1"
              >
                {tag}
              </span>
            ))}
            {tags.length > 3 && (
              <span className="badge-secondary text-xs px-2 py-1">
                +{tags.length - 3}
              </span>
            )}
          </div>
        )}

        {/* Stats and creator */}
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4 text-sm text-secondary-500 dark:text-secondary-400">
            <span className="flex items-center space-x-1">
              <Eye className="w-4 h-4" />
              <span>{model.viewCount}</span>
            </span>
            <span className="flex items-center space-x-1">
              <Download className="w-4 h-4" />
              <span>{model.downloadCount}</span>
            </span>
            <span className="flex items-center space-x-1">
              <Heart className="w-4 h-4" />
              <span>{likeCount}</span>
            </span>
          </div>
          
          <div className="flex items-center space-x-2">
            {creator?.avatar ? (
              <img
                src={creator.avatar}
                alt={creator.username}
                className="w-6 h-6 rounded-full object-cover"
              />
            ) : (
              <div className="w-6 h-6 bg-primary-600 rounded-full flex items-center justify-center">
                <User className="w-3 h-3 text-white" />
              </div>
            )}
            <Link
              to={`/creators/${creator?.username}`}
              className="text-sm text-secondary-600 dark:text-secondary-400 hover:text-primary-600 dark:hover:text-primary-400 transition-colors duration-200"
            >
              {creator?.username}
            </Link>
          </div>
        </div>

        {/* Upload date */}
        <div className="mt-3 pt-3 border-t border-secondary-200 dark:border-secondary-700">
          <div className="flex items-center space-x-1 text-xs text-secondary-500 dark:text-secondary-400">
            <Calendar className="w-3 h-3" />
            <span>{apiUtils.formatRelativeTime(model.createdAt)}</span>
          </div>
        </div>
      </div>
    </div>
  )
}

export default ModelCard
