import React, { useEffect } from 'react'
import { Link, useLocation } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { useTheme } from '../contexts/ThemeContext'
import { Sun, Moon, Loader2, Home, Upload, User, Settings, LogOut, Edit3, BarChart3, Package } from 'lucide-react'
import ThemeToggle from './ui/ThemeToggle'
import UserMenu from './ui/UserMenu'

const Layout = ({ children }) => {
  const { user, profile, loading, isAuthenticated, resetLoading, fetchUserProfile, manualFetchProfile, diagnoseDatabase, logout, forceLogout, resetProfile, forceReset } = useAuth()
  const { theme, toggleTheme } = useTheme()
  const location = useLocation()

  // Reset loading state when user clicks anywhere
  const handlePageClick = () => {
    if (loading) {
      console.log(' Layout: User clicked, resetting loading state')
      resetLoading()
    }
  }

  // Add global click handler to reset stuck loading states
  useEffect(() => {
    const handleGlobalClick = () => {
      if (loading) {
        console.log(' Layout: Global click detected, resetting loading state')
        resetLoading()
      }
    }

    document.addEventListener('click', handleGlobalClick)
    return () => document.removeEventListener('click', handleGlobalClick)
  }, [loading, resetLoading])

  // Manual profile fetch function
  const handleFetchProfile = async () => {
    if (user) {
      console.log(' Layout: Manually fetching profile for user:', user.id)
      await fetchUserProfile(user.id)
    }
  }

  // Manual profile fetch using the new function
  const handleManualFetchProfile = async () => {
    if (user) {
      console.log(' Layout: Using manual fetch profile function for user:', user.id)
      await manualFetchProfile()
    }
  }

  // Database diagnosis function
  const handleDiagnoseDatabase = async () => {
    console.log(' Layout: Starting database diagnosis...')
    await diagnoseDatabase()
  }

  // Test logout function
  const handleTestLogout = async () => {
    if (user) {
      console.log(' Layout: Testing logout...')
      await logout()
    }
  }

  // Force logout function
  const handleForceLogout = async () => {
    if (user) {
      console.log(' Layout: Force logout...')
      await forceLogout()
    }
  }

  // Reset profile function
  const handleResetProfile = () => {
    console.log(' Layout: Resetting profile state...')
    resetProfile()
  }

  // Force reset all states
  const handleForceReset = () => {
    console.log(' Layout: Force resetting all states...')
    forceReset()
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-secondary-50 dark:bg-secondary-900">
        <div className="text-center">
          <Loader2 className="w-12 h-12 animate-spin text-primary-600 mx-auto mb-4" />
          <p className="text-secondary-600 dark:text-secondary-400 mb-4">
            Loading your account...
          </p>
          <div className="space-y-2">
            <button
              onClick={() => window.location.reload()}
              className="px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors mr-2"
            >
              Refresh Page
            </button>
            {process.env.NODE_ENV === 'development' && (
              <button
                onClick={async () => {
                  const { debugAuth } = await import('../contexts/AuthContext')
                  debugAuth()
                }}
                className="px-4 py-2 bg-yellow-600 text-white rounded-lg hover:bg-yellow-700 transition-colors"
              >
                Debug Auth
              </button>
            )}
          </div>
        </div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-secondary-50 dark:bg-secondary-900" onClick={handlePageClick}>
      {/* Debug Panel - Remove this in production */}
      {process.env.NODE_ENV === 'development' && (
        <div className="bg-blue-100 dark:bg-blue-900 border-b border-blue-200 dark:border-blue-700 px-4 py-2 text-xs text-blue-800 dark:text-blue-200">
          <div className="flex items-center justify-between">
            <div>
              <strong>Debug:</strong> Loading: {loading.toString()} | Auth: {isAuthenticated.toString()} | User: {user ? `YES (${user.id})` : 'NO'} | Profile: {profile ? `YES (${profile.username})` : 'NO'}
            </div>
            <div className="flex space-x-2">
              <button
                onClick={handleFetchProfile}
                className="px-2 py-1 bg-green-600 text-white rounded text-xs hover:bg-green-700"
              >
                Fetch Profile
              </button>
              <button
                onClick={handleManualFetchProfile}
                className="px-2 py-1 bg-blue-600 text-white rounded text-xs hover:bg-blue-700"
              >
                Manual Fetch Profile
              </button>
              <button
                onClick={handleDiagnoseDatabase}
                className="px-2 py-1 bg-purple-600 text-white rounded text-xs hover:bg-purple-700"
              >
                Diagnose DB
              </button>
              <button
                onClick={() => {
                  console.log(' Layout: Debug Auth clicked')
                  // This will be handled by the debugAuth function in AuthContext
                }}
                className="px-2 py-1 bg-yellow-600 text-white rounded text-xs hover:bg-yellow-700"
              >
                Debug Auth
              </button>
              <button
                onClick={handleTestLogout}
                className="px-2 py-1 bg-red-600 text-white rounded text-xs hover:bg-red-700"
              >
                Test Logout
              </button>
              <button
                onClick={handleForceLogout}
                className="px-2 py-1 bg-orange-600 text-white rounded text-xs hover:bg-orange-700"
              >
                Force Logout
              </button>
              <button
                onClick={handleResetProfile}
                className="px-2 py-1 bg-gray-600 text-white rounded text-xs hover:bg-gray-700"
              >
                Reset Profile
              </button>
              <button
                onClick={handleForceReset}
                className="px-2 py-1 bg-orange-600 text-white rounded text-xs hover:bg-orange-700"
              >
                Force Reset
              </button>
            </div>
          </div>
        </div>
      )}

      {/* Header */}
      <header className="bg-white dark:bg-secondary-800 shadow-sm border-b border-secondary-200 dark:border-secondary-700">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex justify-between items-center h-16">
            {/* Logo */}
            <Link to="/" className="flex items-center space-x-2">
              <div className="w-8 h-8 bg-primary-600 rounded-lg flex items-center justify-center">
                <span className="text-white font-bold text-lg">3D</span>
              </div>
              <span className="text-xl font-bold text-secondary-900 dark:text-white">3DShareSpace</span>
            </Link>

            {/* Navigation */}
            <nav className="hidden md:flex items-center space-x-8">
              <Link
                to="/"
                className={`flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                  location.pathname === '/'
                    ? 'text-primary-600 bg-primary-50 dark:bg-primary-900/20'
                    : 'text-secondary-700 dark:text-secondary-300 hover:text-secondary-900 dark:hover:text-white'
                }`}
              >
                <Home className="w-4 h-4" />
                <span>Home</span>
              </Link>
              
              <Link
                to="/explore"
                className={`flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                  location.pathname === '/explore'
                    ? 'text-primary-600 bg-primary-50 dark:bg-primary-900/20'
                    : 'text-secondary-700 dark:text-secondary-300 hover:text-secondary-900 dark:hover:text-white'
                }`}
              >
                <Package className="w-4 h-4" />
                <span>Explore</span>
              </Link>

              {isAuthenticated && (
                <>
                  <Link
                    to="/upload"
                    className={`flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                      location.pathname === '/upload'
                        ? 'text-primary-600 bg-primary-50 dark:bg-primary-900/20'
                        : 'text-secondary-700 dark:text-secondary-300 hover:text-secondary-900 dark:hover:text-white'
                    }`}
                  >
                    <Upload className="w-4 h-4" />
                    <span>Upload</span>
                  </Link>
                  
                  <Link
                    to="/dashboard"
                    className={`flex items-center space-x-2 px-3 py-2 rounded-md text-sm font-medium transition-colors ${
                      location.pathname === '/dashboard'
                        ? 'text-primary-600 bg-primary-50 dark:bg-primary-900/20'
                        : 'text-secondary-700 dark:text-secondary-300 hover:text-secondary-900 dark:hover:text-white'
                    }`}
                  >
                    <BarChart3 className="w-4 h-4" />
                    <span>Dashboard</span>
                  </Link>
                </>
              )}
            </nav>

            {/* Right side */}
            <div className="flex items-center space-x-4">
              <ThemeToggle />
              {isAuthenticated ? (
                <UserMenu />
              ) : (
                <div className="flex items-center space-x-2">
                  <Link
                    to="/login"
                    className="px-4 py-2 text-secondary-700 dark:text-secondary-300 hover:text-secondary-900 dark:hover:text-white transition-colors"
                  >
                    Login
                  </Link>
                  <Link
                    to="/signup"
                    className="px-4 py-2 bg-primary-600 text-white rounded-lg hover:bg-primary-700 transition-colors"
                  >
                    Sign Up
                  </Link>
                </div>
              )}
            </div>
          </div>
        </div>
      </header>

      {/* Main content */}
      <main className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        {children}
      </main>

      {/* Footer */}
      <footer className="bg-white dark:bg-secondary-800 border-t border-secondary-200 dark:border-secondary-700 mt-auto">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
          <div className="text-center text-secondary-600 dark:text-secondary-400">
            <p>&copy; 2024 3DShareSpace. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </div>
  )
}

export default Layout
