// Upload service
export const uploadService = {
  uploadModel: (formData) => 
    api.post('/upload', formData, {
      headers: { 'Content-Type': 'multipart/form-data' },
      timeout: 300000, // 5 minutes for large files
    }).then(res => res.data),
  
  updateModel: (id, data) => 
    api.put(`/upload/${id}`, data).then(res => res.data),
  
  deleteModel: (id) => 
    api.delete(`/upload/${id}`).then(res => res.data),
  
  getMyModels: (params = {}) => 
    api.get('/upload/my-models', { params }).then(res => res.data),
}

