import { useState, useRef, useCallback } from 'react'
import { useNavigate } from 'react-router-dom'
import { useAuth } from '../contexts/AuthContext'
import { supabase } from '../lib/supabase'
import { 
  Upload as UploadIcon, 
  X, 
  File, 
  Image, 
  Tag, 
  Type, 
  FileText,
  AlertCircle,
  CheckCircle,
  Loader2
} from 'lucide-react'
import toast from 'react-hot-toast'

const Upload = () => {
  const { user, profile, isAuthenticated } = useAuth()
  const navigate = useNavigate()
  const modelInputRef = useRef(null)
  const thumbnailInputRef = useRef(null)
  const previewInputRef = useRef(null)
  const [isUploading, setIsUploading] = useState(false)
  const [uploadProgress, setUploadProgress] = useState(0)
  
  const [formData, setFormData] = useState({
    title: '',
    description: '',
    category: 'Characters',
    tags: '',
    price: 0,
    isFree: true
  })
  
  const [files, setFiles] = useState({
    model: null,
    thumbnail: null,
    preview: null
  })
  
  const [dragActive, setDragActive] = useState(false)
  const [errors, setErrors] = useState({})

  // Handle drag events
  const handleDrag = useCallback((e) => {
    e.preventDefault()
    e.stopPropagation()
    if (e.type === "dragenter" || e.type === "dragover") {
      setDragActive(true)
    } else if (e.type === "dragleave") {
      setDragActive(false)
    }
  }, [])

  const handleDrop = useCallback((e) => {
    e.preventDefault()
    e.stopPropagation()
    setDragActive(false)
    
    if (e.dataTransfer.files && e.dataTransfer.files[0]) {
      handleFileSelect(e.dataTransfer.files[0], 'model')
    }
  }, [])

  // Check if user is authenticated
  if (!isAuthenticated) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="text-center">
          <h2 className="text-2xl font-bold text-secondary-900 dark:text-white mb-4">
            Authentication Required
          </h2>
          <p className="text-secondary-600 dark:text-secondary-400 mb-6">
            You need to be logged in to upload 3D models.
          </p>
          <button
            onClick={() => navigate('/login')}
            className="btn-primary"
          >
            Sign In
          </button>
        </div>
      </div>
    )
  }

  // Handle file selection
  const handleFileSelect = (file, type) => {
    console.log(' File selected:', file.name, 'Type:', type, 'Size:', (file.size / 1024 / 1024).toFixed(2), 'MB')
    
    const allowedModelTypes = ['obj', 'fbx', 'glb', 'gltf', 'dae', 'blend', '3ds', 'max', 'ma', 'mb']
    const allowedImageTypes = ['jpg', 'jpeg', 'png', 'webp', 'gif']
    
    const fileExtension = file.name.split('.').pop().toLowerCase()
    console.log(' File extension:', fileExtension)
    
    if (type === 'model') {
      if (!allowedModelTypes.includes(fileExtension)) {
        console.error(' Invalid model file type:', fileExtension)
        toast.error(`Invalid model file type. Allowed: ${allowedModelTypes.join(', ')}`)
        return
      }
      if (file.size > 100 * 1024 * 1024) { // 100MB limit
        console.error(' File too large:', (file.size / 1024 / 1024).toFixed(2), 'MB')
        toast.error('Model file size must be less than 100MB')
        return
      }
      console.log(' Model file validation passed')
    } else if (type === 'thumbnail' || type === 'preview') {
      if (!allowedImageTypes.includes(fileExtension)) {
        console.error(' Invalid image file type:', fileExtension)
        toast.error(`Invalid image file type. Allowed: ${allowedImageTypes.join(', ')}`)
        return
      }
      if (file.size > 10 * 1024 * 1024) { // 10MB limit
        console.error(' Image file too large:', (file.size / 1024 / 1024).toFixed(2), 'MB')
        toast.error('Image file size must be less than 10MB')
        return
      }
      console.log(' Image file validation passed')
    }
    
    setFiles(prev => ({ ...prev, [type]: file }))
    setErrors(prev => ({ ...prev, [type]: null }))
    console.log(' File set successfully')
  }

  // Handle form input changes
  const handleInputChange = (e) => {
    const { name, value, type, checked } = e.target
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }))
    setErrors(prev => ({ ...prev, [name]: null }))
  }

  // Validate form
  const validateForm = () => {
    const newErrors = {}
    
    if (!formData.title.trim()) newErrors.title = 'Title is required'
    if (!formData.description.trim()) newErrors.description = 'Description is required'
    if (!formData.category) newErrors.category = 'Category is required'
    if (!files.model) newErrors.model = '3D model file is required'
    
    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  // Handle form submission
  const handleSubmit = async (e) => {
    e.preventDefault()
    
    console.log(' Form submitted, starting validation...')
    
    if (!validateForm()) {
      console.log(' Form validation failed')
      toast.error('Please fix the errors before submitting')
      return
    }
    
    console.log(' Form validation passed, starting upload...')
    
    setIsUploading(true)
    setUploadProgress(0)
    
    try {
      console.log(' Starting upload process...')
      console.log('User ID:', user.id)
      console.log('File:', files.model)
      console.log('Form data:', formData)
      
      // Test Supabase connection first
      console.log(' Testing Supabase connection...')
      const { data: testData, error: testError } = await supabase
        .from('models')
        .select('count')
        .limit(1)
      
      if (testError) {
        console.error(' Database connection failed:', testError)
        throw new Error(`Database connection failed: ${testError.message}`)
      }
      console.log(' Database connection successful')
      
      // Test storage access
      console.log(' Testing storage access...')
      const { data: storageTest, error: storageTestError } = await supabase.storage
        .from('models')
        .list('', { limit: 1 })
      
      if (storageTestError) {
        console.error(' Storage access failed:', storageTestError)
        throw new Error(`Storage access failed: ${storageTestError.message}`)
      }
      console.log(' Storage access successful')
      
      setUploadProgress(10)
      
      // Upload model file to Supabase Storage
      console.log(' Uploading model file...')
      const modelFileName = `${Date.now()}_${files.model.name}`
      console.log('Model filename:', modelFileName)
      console.log('File size:', (files.model.size / 1024 / 1024).toFixed(2), 'MB')
      
      // Add timeout to prevent hanging
      const uploadPromise = supabase.storage
        .from('models')
        .upload(modelFileName, files.model)
      
      const timeoutPromise = new Promise((_, reject) => {
        setTimeout(() => reject(new Error('Upload timeout after 30 seconds')), 30000)
      })
      
      const { data: modelData, error: modelError } = await Promise.race([
        uploadPromise,
        timeoutPromise
      ])
      
      if (modelError) {
        console.error(' Model upload failed:', modelError)
        throw modelError
      }
      
      console.log(' Model upload successful:', modelData)
      setUploadProgress(30)
      
      // Get model file URL
      const { data: { publicUrl: modelUrl } } = supabase.storage
        .from('models')
        .getPublicUrl(modelFileName)
      
      console.log('Model URL:', modelUrl)
      
      // Upload thumbnail if provided
      let thumbnailUrl = null
      if (files.thumbnail) {
        console.log(' Uploading thumbnail...')
        const thumbnailFileName = `${Date.now()}_${files.thumbnail.name}`
        const { data: thumbnailData, error: thumbnailError } = await supabase.storage
          .from('thumbnails')
          .upload(thumbnailFileName, files.thumbnail)
        
        if (thumbnailError) throw thumbnailError
        
        const { data: { publicUrl } } = supabase.storage
          .from('thumbnails')
          .getPublicUrl(thumbnailFileName)
        
        thumbnailUrl = publicUrl
        console.log(' Thumbnail upload successful')
      }
      
      setUploadProgress(60)
      
      // Upload preview image if provided
      let previewUrl = null
      if (files.preview) {
        console.log(' Uploading preview...')
        const previewFileName = `${Date.now()}_${files.preview.name}`
        const { data: previewData, error: previewError } = await supabase.storage
          .from('previews')
          .upload(previewFileName, files.preview)
        
        if (previewError) throw previewError
        
        const { data: { publicUrl } } = supabase.storage
          .from('previews')
          .getPublicUrl(previewFileName)
        
        previewUrl = publicUrl
        console.log(' Preview upload successful')
      }
      
      setUploadProgress(80)
      
      // Create model record in database
      console.log(' Creating database record...')
      const modelRecordData = {
        title: formData.title,
        description: formData.description,
        category: formData.category,
        tags: formData.tags,
        file_url: modelUrl,
        thumbnail_url: thumbnailUrl,
        preview_url: previewUrl,
        file_size: files.model.size,
        file_type: files.model.name.split('.').pop().toLowerCase(),
        user_id: user.id,
        is_featured: false
      }
      
      console.log('Model record data:', modelRecordData)
      console.log('Model record data (JSON):', JSON.stringify(modelRecordData, null, 2))
      
      // Check if all required fields are present
      console.log(' Field validation:')
      console.log('- title:', !!modelRecordData.title, 'value:', modelRecordData.title)
      console.log('- description:', !!modelRecordData.description, 'value:', modelRecordData.description)
      console.log('- category:', !!modelRecordData.category, 'value:', modelRecordData.category)
      console.log('- file_url:', !!modelRecordData.file_url, 'value:', modelRecordData.file_url)
      console.log('- user_id:', !!modelRecordData.user_id, 'value:', modelRecordData.user_id)
      
      const { data: modelRecord, error: dbError } = await supabase
        .from('models')
        .insert(modelRecordData)
        .select()
        .single()
      
      if (dbError) {
        console.error(' Database insert failed:', dbError)
        console.error(' Error details:', {
          message: dbError.message,
          details: dbError.details,
          hint: dbError.hint,
          code: dbError.code
        })
        throw dbError
      }
      
      console.log(' Database record created:', modelRecord)
      setUploadProgress(100)
      
      toast.success('3D model uploaded successfully!')
      navigate(`/models/${modelRecord.id}`)
      
    } catch (error) {
      console.error(' Upload error:', error)
      toast.error(error.message || 'Failed to upload model')
    } finally {
      console.log(' Upload process finished (success or failure)')
      setIsUploading(false)
      setUploadProgress(0)
    }
  }

  // Remove file
  const removeFile = (type) => {
    setFiles(prev => ({ ...prev, [type]: null }))
    setErrors(prev => ({ ...prev, [type]: null }))
  }

  return (
    <div className="max-w-4xl mx-auto">
      <div className="mb-8">
        <h1 className="text-3xl font-bold text-secondary-900 dark:text-white mb-2">
        Upload 3D Model
      </h1>
      <p className="text-secondary-600 dark:text-secondary-400">
        Share your 3D models with the community. Upload files, add descriptions, and set categories.
      </p>
        
        {/* Test Button */}
        <div className="mt-4 flex space-x-2">
          <button
            type="button"
            onClick={async () => {
              try {
                console.log(' Testing Supabase connection...')
                const { data, error } = await supabase
                  .from('models')
                  .select('count')
                  .limit(1)
                
                if (error) {
                  console.error(' Test failed:', error)
                  toast.error(`Test failed: ${error.message}`)
                } else {
                  console.log(' Test successful:', data)
                  toast.success('Supabase connection working!')
                }
              } catch (err) {
                console.error(' Test error:', err)
                toast.error(`Test error: ${err.message}`)
              }
            }}
            className="bg-yellow-500 text-white px-4 py-2 rounded-lg hover:bg-yellow-600 text-sm"
          >
             Test Supabase Connection
          </button>
          
          <button
            type="button"
            onClick={async () => {
              try {
                console.log(' Testing storage access...')
                const { data, error } = await supabase.storage
                  .from('models')
                  .list('', { limit: 1 })
                
                if (error) {
                  console.error(' Storage test failed:', error)
                  toast.error(`Storage test failed: ${error.message}`)
                } else {
                  console.log(' Storage test successful:', data)
                  toast.success('Storage access working!')
                }
              } catch (err) {
                console.error(' Storage test error:', err)
                toast.error(`Storage test error: ${err.message}`)
              }
            }}
            className="bg-green-500 text-white px-4 py-2 rounded-lg hover:bg-green-600 text-sm"
          >
             Test Storage Access
          </button>
          
          <button
            type="button"
            onClick={async () => {
              try {
                console.log(' Testing file upload...')
                // Create a simple test file using Blob and File constructor safely
                const testContent = 'test content for upload'
                const testBlob = new Blob([testContent], { type: 'text/plain' })
                
                // Check if File constructor is available
                if (typeof File !== 'undefined') {
                  const testFile = new File([testBlob], 'test.txt', { type: 'text/plain' })
                  console.log('Test file created:', testFile)
                  
                  const { data, error } = await supabase.storage
                    .from('models')
                    .upload(`test_${Date.now()}.txt`, testFile)
                  
                  if (error) {
                    console.error(' File upload test failed:', error)
                    toast.error(`Upload test failed: ${error.message}`)
                  } else {
                    console.log(' File upload test successful:', data)
                    toast.success('File upload working!')
                    
                    // Clean up test file
                    await supabase.storage
                      .from('models')
                      .remove([data.path])
                  }
                } else {
                  // Fallback: use Blob directly
                  console.log('File constructor not available, using Blob directly')
                  const { data, error } = await supabase.storage
                    .from('models')
                    .upload(`test_${Date.now()}.txt`, testBlob, {
                      contentType: 'text/plain'
                    })
                  
                  if (error) {
                    console.error(' Blob upload test failed:', error)
                    toast.error(`Blob upload test failed: ${error.message}`)
                  } else {
                    console.log(' Blob upload test successful:', data)
                    toast.success('Blob upload working!')
                    
                    // Clean up test file
                    await supabase.storage
                      .from('models')
                      .remove([data.path])
                  }
                }
              } catch (err) {
                console.error(' File upload test error:', err)
                toast.error(`Upload test error: ${err.message}`)
              }
            }}
            className="bg-blue-500 text-white px-4 py-2 rounded-lg hover:bg-blue-600 text-sm"
          >
             Test File Upload
          </button>
          
          <button
            type="button"
            onClick={async () => {
              try {
                console.log(' Testing database schema...')
                // Get table info
                const { data, error } = await supabase
                  .from('models')
                  .select('*')
                  .limit(0)
                
                if (error) {
                  console.error(' Schema test failed:', error)
                  toast.error(`Schema test failed: ${error.message}`)
                } else {
                  console.log(' Schema test successful')
                  console.log('Table structure:', data)
                  
                  // Try to get column info
                  const { data: columns, error: colError } = await supabase
                    .rpc('get_table_columns', { table_name: 'models' })
                    .catch(() => ({ data: null, error: 'RPC not available' }))
                  
                  if (columns) {
                    console.log('Table columns:', columns)
                  } else {
                    console.log('RPC not available, trying direct query...')
                    // Try a simple insert to see what fields are missing
                    const testData = {
                      title: 'Test Model',
                      description: 'Test Description',
                      category: 'Props',
                      file_url: 'https://example.com/test.glb',
                      user_id: user.id
                    }
                    
                    const { error: testInsertError } = await supabase
                      .from('models')
                      .insert(testData)
                    
                    if (testInsertError) {
                      console.log('Test insert error:', testInsertError)
                    } else {
                      console.log('Test insert successful')
                      // Clean up test record
                      await supabase
                        .from('models')
                        .delete()
                        .eq('title', 'Test Model')
                    }
                  }
                }
              } catch (err) {
                console.error(' Schema test error:', err)
                toast.error(`Schema test error: ${err.message}`)
              }
            }}
            className="bg-purple-500 text-white px-4 py-2 rounded-lg hover:bg-purple-600 text-sm"
          >
             Test Database Schema
          </button>
          
          <button
            type="button"
            onClick={async () => {
              try {
                console.log(' Testing minimal insert...')
                // Try to insert minimal data to see what columns are missing
                const minimalData = {
                  title: 'Test',
                  description: 'Test',
                  category: 'Props',
                  file_url: 'https://example.com/test.glb',
                  user_id: user.id
                }
                
                console.log('Trying to insert:', minimalData)
                
                const { data, error } = await supabase
                  .from('models')
                  .insert(minimalData)
                  .select()
                
                if (error) {
                  console.error(' Minimal insert failed:', error)
                  console.error('Error details:', {
                    message: error.message,
                    details: error.details,
                    hint: error.hint,
                    code: error.code
                  })
                } else {
                  console.log(' Minimal insert successful:', data)
                  // Clean up
                  await supabase
                    .from('models')
                    .delete()
                    .eq('id', data[0].id)
                }
              } catch (err) {
                console.error(' Structure test error:', err)
              }
            }}
            className="bg-orange-500 text-white px-4 py-2 rounded-lg hover:bg-orange-600 text-sm"
          >
             Test Minimal Insert
          </button>
        </div>
      </div>

      <form onSubmit={handleSubmit} className="space-y-8">
        {/* File Upload Section */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4">
            Model Files
          </h2>
          
          {/* 3D Model Upload */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
              3D Model File * <span className="text-red-500">Required</span>
            </label>
            
            <div
              className={`border-2 border-dashed rounded-lg p-8 text-center transition-colors ${
                dragActive 
                  ? 'border-primary-500 bg-primary-50 dark:bg-primary-900/20' 
                  : 'border-secondary-300 dark:border-secondary-600'
              }`}
              onDragEnter={handleDrag}
              onDragLeave={handleDrag}
              onDragOver={handleDrag}
              onDrop={handleDrop}
            >
              {files.model ? (
                <div className="space-y-4">
                  <div className="flex items-center justify-center space-x-2 text-green-600">
                    <CheckCircle className="w-6 h-6" />
                    <span className="font-medium">{files.model.name}</span>
                  </div>
                  <p className="text-sm text-secondary-500">
                    Size: {(files.model.size / 1024 / 1024).toFixed(2)} MB
                  </p>
                  <button
                    type="button"
                    onClick={() => removeFile('model')}
                    className="text-red-600 hover:text-red-700 text-sm"
                  >
                    Remove File
                  </button>
                </div>
              ) : (
                <div className="space-y-4">
                  <UploadIcon className="w-12 h-12 text-secondary-400 mx-auto" />
                  <div>
                    <p className="text-lg font-medium text-secondary-900 dark:text-white">
                      Drop your 3D model here
                    </p>
                    <p className="text-sm text-secondary-500">
                      or click to browse files
                    </p>
                  </div>
                  <button
                    type="button"
                    onClick={() => modelInputRef.current?.click()}
                    className="btn-primary"
                  >
                    Choose File
                  </button>
                  <input
                    ref={modelInputRef}
                    type="file"
                    accept=".obj,.fbx,.glb,.gltf,.dae,.blend,.3ds,.max,.ma,.mb"
                    onChange={(e) => e.target.files?.[0] && handleFileSelect(e.target.files[0], 'model')}
                    className="hidden"
                  />
                  <p className="text-xs text-secondary-400">
                    Supported formats: OBJ, FBX, GLB, GLTF, DAE, BLEND, 3DS, MAX, MA, MB (Max 100MB)
                  </p>
                </div>
              )}
            </div>
            {errors.model && (
              <p className="mt-2 text-sm text-red-600 flex items-center">
                <AlertCircle className="w-4 h-4 mr-1" />
                {errors.model}
              </p>
            )}
          </div>

          {/* Thumbnail Upload */}
          <div className="mb-6">
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
              Thumbnail Image (Optional)
            </label>
            
            <div className="border-2 border-dashed border-secondary-300 dark:border-secondary-600 rounded-lg p-6 text-center">
              {files.thumbnail ? (
                <div className="space-y-4">
                  <img 
                    src={URL.createObjectURL(files.thumbnail)} 
                    alt="Thumbnail preview" 
                    className="w-32 h-32 object-cover rounded mx-auto"
                  />
                  <div className="flex items-center justify-center space-x-2">
                    <span className="text-sm text-secondary-600">{files.thumbnail.name}</span>
                    <button
                      type="button"
                      onClick={() => removeFile('thumbnail')}
                      className="text-red-600 hover:text-red-700"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                </div>
              ) : (
                <div className="space-y-4">
                  <Image className="w-8 h-8 text-secondary-400 mx-auto" />
                  <button
                    type="button"
                    onClick={() => thumbnailInputRef.current?.click()}
                    className="btn-secondary"
                  >
                    Choose Thumbnail
                  </button>
                  <input
                    ref={thumbnailInputRef}
                    type="file"
                    accept="image/*"
                    onChange={(e) => e.target.files?.[0] && handleFileSelect(e.target.files[0], 'thumbnail')}
                    className="hidden"
                  />
                </div>
              )}
            </div>
          </div>

          {/* Preview Image Upload */}
          <div>
            <label className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
              Preview Image (Optional)
            </label>
            
            <div className="border-2 border-dashed border-secondary-300 dark:border-secondary-600 rounded-lg p-6 text-center">
              {files.preview ? (
                <div className="space-y-4">
                  <img 
                    src={URL.createObjectURL(files.preview)} 
                    alt="Preview" 
                    className="w-32 h-32 object-cover rounded mx-auto"
                  />
                  <div className="flex items-center justify-center space-x-2">
                    <span className="text-sm text-secondary-600">{files.preview.name}</span>
                    <button
                      type="button"
                      onClick={() => removeFile('preview')}
                      className="text-red-600 hover:text-red-700"
                    >
                      <X className="w-4 h-4" />
                    </button>
                  </div>
                </div>
              ) : (
                <div className="space-y-4">
                  <Image className="w-8 h-8 text-secondary-400 mx-auto" />
                  <button
                    type="button"
                    onClick={() => previewInputRef.current?.click()}
                    className="btn-secondary"
                  >
                    Choose Preview
                  </button>
                  <input
                    ref={previewInputRef}
                    type="file"
                    accept="image/*"
                    onChange={(e) => e.target.files?.[0] && handleFileSelect(e.target.files[0], 'preview')}
                    className="hidden"
                  />
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Model Information Section */}
        <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
          <h2 className="text-xl font-semibold text-secondary-900 dark:text-white mb-4">
            Model Information
          </h2>
          
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {/* Title */}
            <div className="md:col-span-2">
              <label htmlFor="title" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Title *
              </label>
              <input
                type="text"
                id="title"
                name="title"
                value={formData.title}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                placeholder="Enter model title"
              />
              {errors.title && (
                <p className="mt-1 text-sm text-red-600">{errors.title}</p>
              )}
            </div>

            {/* Description */}
            <div className="md:col-span-2">
              <label htmlFor="description" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Description *
              </label>
              <textarea
                id="description"
                name="description"
                rows="4"
                value={formData.description}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                placeholder="Describe your 3D model..."
              />
              {errors.description && (
                <p className="mt-1 text-sm text-red-600">{errors.description}</p>
              )}
            </div>

            {/* Category */}
            <div>
              <label htmlFor="category" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Category *
              </label>
              <select
                id="category"
                name="category"
                value={formData.category}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
              >
                <option value="Characters">Characters</option>
                <option value="Environments">Environments</option>
                <option value="Props">Props</option>
                <option value="Vehicles">Vehicles</option>
                <option value="Weapons">Weapons</option>
                <option value="Architecture">Architecture</option>
                <option value="Nature">Nature</option>
                <option value="Sci-Fi">Sci-Fi</option>
                <option value="Fantasy">Fantasy</option>
                <option value="Other">Other</option>
              </select>
            </div>

            {/* Tags */}
            <div>
              <label htmlFor="tags" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                Tags
              </label>
              <input
                type="text"
                id="tags"
                name="tags"
                value={formData.tags}
                onChange={handleInputChange}
                className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                placeholder="tag1, tag2, tag3"
              />
              <p className="mt-1 text-xs text-secondary-500">
                Separate tags with commas
              </p>
            </div>

            {/* Pricing */}
            <div className="md:col-span-2">
              <div className="flex items-center space-x-4">
                <label className="flex items-center">
                  <input
                    type="checkbox"
                    name="isFree"
                    checked={formData.isFree}
                    onChange={handleInputChange}
                    className="mr-2"
                  />
                  <span className="text-sm text-secondary-700 dark:text-secondary-300">
                    This model is free
                  </span>
                </label>
              </div>
              
              {!formData.isFree && (
                <div className="mt-4">
                  <label htmlFor="price" className="block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-2">
                    Price ($)
                  </label>
                  <input
                    type="number"
                    id="price"
                    name="price"
                    value={formData.price}
                    onChange={handleInputChange}
                    min="0"
                    step="0.01"
                    className="w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white"
                    placeholder="0.00"
                  />
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Upload Progress */}
        {isUploading && (
          <div className="bg-white dark:bg-secondary-800 rounded-lg border border-secondary-200 dark:border-secondary-700 p-6">
            <div className="flex items-center space-x-3 mb-4">
              <Loader2 className="w-6 h-6 text-primary-600 animate-spin" />
              <span className="text-lg font-medium text-secondary-900 dark:text-white">
                Uploading your 3D model...
              </span>
            </div>
            <div className="w-full bg-secondary-200 dark:bg-secondary-700 rounded-full h-2">
              <div 
                className="bg-primary-600 h-2 rounded-full transition-all duration-300"
                style={{ width: `${uploadProgress}%` }}
              ></div>
            </div>
            <p className="mt-2 text-sm text-secondary-600 dark:text-secondary-400">
              {uploadProgress}% complete
            </p>
          </div>
        )}

        {/* Submit Button */}
        <div className="flex justify-end space-x-4">
          <button
            type="button"
            onClick={() => navigate('/')}
            className="btn-secondary"
            disabled={isUploading}
          >
            Cancel
          </button>
          <button
            type="submit"
            className="btn-primary"
            disabled={isUploading}
          >
            {isUploading ? (
              <>
                <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                Uploading...
              </>
            ) : (
              <>
                <UploadIcon className="w-4 h-4 mr-2" />
                Upload Model
              </>
            )}
          </button>
        </div>
      </form>
    </div>
  )
}

export default Upload
